<template>
  <div class="interest">
    <div class="intHeader">
      <div class="cancel" @click="goBack()">
        {{cancel}}
      </div>
      <div class="title">
        {{title}}
      </div>
      <div class="finish" @click="submit()">
        {{finish}}
      </div>
    </div>
    <div class="spaceLine"></div>
    <div class="articleListCol" ref="articleListCol">
      <div class="text" :class="{textClick:item.checked}" v-for="(item,index) in textContent" @click="changeClass(item)" :key="index">
        <div class="content" :class="{fontColor:item.checked}">
          {{item.content}}
        </div>
        <div class="clickIcon" v-if="item.checked">
          <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16">
            <g fill="none" fill-rule="evenodd">
              <path d="M0 0h16v16H0z"/>
              <path fill="#20BCC4" d="M6.177 11.301L12.167 5l.833.85L6.177 13 3 9.658l.833-.85z"/>
            </g>
          </svg>
        </div>
      </div>
    </div>
    <CommonModal :show="isChecked" :patternName="modalPattern" @close="closeModal()"/>
  </div>
</template>

<script>
import CommonModal from '@/components/cmptsModal/CommonModal.vue'
//import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
    
  export default {
    name: 'interestType',
    components:{
//      PullTo,
      CommonModal
    },
    mounted: function () {
      this.init()
      for (var i = 0; i < this.textContent.length; i++) {
        this.array[i] = this.textContent[i].checked
      }
    },
    data() {
      return {
        cancel: 'キャンセル',
        title: '関心ジャンルを追加',
        finish: '完了',
        modalPattern: 'noSelect',
        pageDivCss: 'margin-top:84px',
        textClick: false,
        message: 0,
        isChecked: false,
        array: [],
        textContent: [{content:'テキストテキストテキスト', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false},
                      {content:'ジャンルの名前', checked: false}]
      }
    },
    methods: {
      init() {
        this.$refs.articleListCol.style.height = window.innerHeight - 42 + 'px';
      },
      changeClass: function (item) {
          item.checked = !item.checked
      },
      goBack() {
//        for (var i = 0; i< this.textContent.length; i++) {
//          this.textContent[i].checked = this.array[i]
//        }
        if (window.history.length > 1) {
          this.$router.go(-1)
        } else {
          this.$router.push('/')
        }
      },
      submit() {
        this.message = 0
        for (var i = 0; i < this.textContent.length; i++) {
          if (this.textContent[i].checked) {
            this.message = this.message + 1
          }
        }
        if (!this.message) {
          this.isChecked = true
        } else {
          this.$store.commit('setInterestArray', this.textContent)
          if (window.history.length > 1) {
            this.$router.go(-1)
          } else {
            this.$router.push('/')
          }
        }
      },
      closeModal() {
        this.isChecked = false
      }
    },
  }
</script>

<style scoped>
.interest {
  background-color: #f2f5f7;
}

.interest .articleListCol {
  overflow: auto;
}

.interest .intHeader {
  width: 100%;
  height: 42px;
  box-shadow: inset 0 -1px 0 0 #e7e7e7;
  background-color: #ffffff;
  position: fixed;
  z-index: 5;
}

.interest .intHeader .cancel {
  width: 60px;
  height: 18px;
  font-family: NotoSansCJKjp;
  font-size: 12px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #0076ff;
  position: absolute;
  top: 12px;
  left: 12px;
}

.interest .intHeader .title {
  width: 144px;
  height: 24px;
  font-family: NotoSansCJKjp;
  font-size: 16px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #333333;
  position: absolute;
  top: 8px;
  left: calc((100% - 143px)/2);
}

.interest .intHeader .finish {
  width: 24px;
  height: 18px;
  font-family: NotoSansCJKjp;
  font-size: 12px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #0076ff;
  position: absolute;
  top: 12px;
  right: 12px;
}

.interest .spaceLine {
    height: 42px;
}

.interest .text {
  width: 100%;
  height: 48px;
  box-shadow: inset 0 -1px 0 0 #e6e6e6;
  background-color: #f7f7f7;
  position: relative;
}

.interest .textClick{
  width: 100%;
  height: 48px;
  box-shadow: inset 0 -1px 0 0 #e6e6e6;
  background-color: #303747;
  color: #ffffff;
}

.interest .text .content{
  width: 168px;
  height: 20px;
  font-family: NotoSansCJKjp;
  font-size: 14px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #333333;
  position: absolute;
  top: 14px;
  left: 12px;
}

.interest .text .fontColor{
  color: #ffffff;
}

.interest .text .clickIcon{
  position: absolute;
  top: 14px;
  right: 12px;
}
</style>