            this.offClick = false
            this.activityJudgNewID = true
            this.dateIdNew = true
            this.disabledR1=false
            this.disabledR2=false
            this.disabledR3=false
            this.disabledC1=false
            this.disabledC2=false
            this.disabledC3=false
        },

        buttonTest(){
            // this.onClick = true
            //  this.offClick = true
             this.inoff=true,
             this.inout=false

        },
        buttonTest2(){
             this.inoff=false,
             this.inout=true  
        },
        goBack() {
        if (window.history.length > 1) {
          this.$router.go(-1)
        } else {
          this.$router.push('/')
        }
      },
       submit() {
        this.message=0
        if (!this.message) {
          this.isChecked=true
        } else {
          this.$store.commit('setOverSeasArray', this.siteType);
          this.goBack();
        }
      },
      closeModal() {
        this.isChecked=false
      }
