<template>
  <div class="overseas">
    <div class="header">
      <div class="cancel" @click="goBack()">
        {{cancel}}
      </div>
      <div class="title">
        {{title}}
      </div>
      <div class="finish" @click="submit()">
        {{finish}}
      </div>
    </div>
    <div class="spaceLine"></div>
    <div class="listScroll" ref="listScroll">
      <div v-for="(item,index) in siteType" :key="index">
        <div class="content"  @click="siteSelect(item)">
          <div class="siteContent" :class="{isSiteClick:item.isClick}">
            {{item.name}}
          </div>
          <div class="iconStyle">
            <img src="@/assets/common/up.png" v-show="item.upShow">
            <img src="@/assets/common/down.png" v-show="item.downShow">
          </div>
        </div>
        <div v-if="item.listClick">
          <div class="button">
            <button class="selectAll" :class="{butSelChange:item.butSelChange}" @click="selectAllList(item)">
              {{selectAll}}
            </button>
            <button class="cancelAll" :class="{butCanChange:item.butCanChange}" @click="cancelAllList(item)">
              {{cancelAll}}
            </button>
          </div>
          <div v-for="(siteList,conIndex) in item.type" class="contentStyle" :class="{textClick:siteList.checked}" @click="textClick(siteList,item)" :key="conIndex">
            <div class="contentFont" :class="{fontColor:siteList.checked}">
              {{siteList.content}}
            </div>
            <div class="clickIcon" v-if="siteList.checked">
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16">
                <g fill="none" fill-rule="evenodd">
                  <path d="M0 0h16v16H0z"/>
                  <path fill="#20BCC4" d="M6.177 11.301L12.167 5l.833.85L6.177 13 3 9.658l.833-.85z"/>
                </g>
              </svg>
            </div>
            <div class="lineStyle" v-if="item.type.length > conIndex + 1"></div>
          </div>
        </div>
      </div>
    </div>
    <CommonModal :show="isChecked" :patternName="modalPattern" @close="closeModal()"/>
  </div>
</template>

<script>
import CommonModal from '@/components/cmptsModal/CommonModal.vue'
//import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
    
  export default {
    name: 'overseas',
    components:{
      CommonModal,
//      PullTo
    },
    data() {
      return {
        message: 0,
        isChecked: false,
        modalPattern: 'noSelect',
        isClick: 0,
        butSelChange: false,
        butCanChange: false,
        cancel: 'キャンセル',
        title: '関心ジャンルを追加',
        finish: '完了',
        selectAll: 'すべてを選択する',
        cancelAll: 'すべての選択を解除する',
        siteType: [{name: '(全般)',
                    rotate: 'rotate(90)',
                    upShow: false,
                    downShow: true,
                    listClick: false,
                    type: [{content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false}
                          ]
                   },
                   {name: '(サイト分類)',
                    rotate: 'rotate(90)',
                    upShow: false,
                    downShow: true,
                    listClick: false,
                    type: [{content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false}
                          ]
                   },
                   {name: '(サイト分類)',
                    rotate: 'rotate(90)',
                    upShow: false,
                    downShow: true,
                    listClick: false,
                    type: [{content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false}
                          ]
                   },
                   
                   {name: '(サイト分類)',
                    rotate: 'rotate(90)',
                    upShow: false,
                    downShow: true,
                    listClick: false,
                    type: [{content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false},
                           {content: 'サイト名前が表示エリアより長い場合はmarquee', checked: false}
                          ]
                   }],
      }
    },
    activated() {
      this.init()
    },
    methods: {
      init() {
        this.$refs.listScroll.style.height = window.innerHeight - 42 + 'px';
        for (var i = 0; i < this.siteType.length; i++) {
          for (var j = 0; j < this.siteType[i].type.length; j++) {
            this.siteType[i].listClick = false
            this.siteType[i].butSelChange = false
            this.siteType[i].butCanChange = false
//            this.siteType[i].isClick = 0
            this.siteType[i].rotate = "rotate(90)"
          }
        }
//        alert(this.$store.state.overSeasData)
      },
      goBack() {
//        this.init()
//        for (var i = 0; i < this.siteType.length; i++) {
//          for (var j = 0; j < this.siteType[i].type.length; j++) {
//            this.siteType[i].type[j].checked = false
//          }
//        }
        if (window.history.length > 1) {
          this.$router.go(-1)
        } else {
          this.$router.push('/')
        }
      },
      submit() {
        this.message = 0
        for (var i = 0; i < this.siteType.length; i++) {
          this.siteType[i].listClick = false
          this.siteType[i].rotate = "rotate(90)"
          for (var j = 0; j < this.siteType[i].type.length; j++) {
            if (this.siteType[i].type[j].checked) {
              this.message = this.message + 1
            }
          }
        }
        if (!this.message) {
          this.isChecked = true
        } else {
          this.$store.commit('setOverSeasArray', this.siteType);
          this.goBack();
        }
      },
      siteSelect(item) {
        if (item.downShow) {
          item.upShow = true
          item.downShow = false
          item.listClick = true
        } else {
          item.upShow = false
          item.downShow = true
          item.listClick = false
        }
      },
      textClick(siteList,item) {
        if(typeof siteList.checked == 'undefined') {
          this.$set(siteList, "checked", true)
        } else {
          siteList.checked = !siteList.checked
        }
        item.isClick = this.isClick
        for (var i = 0; i < item.type.length; i++) {
          if (item.type[i].checked) {
            item.isClick++
            item.butCanChange = true
            if (item.isClick == item.type.length) {
              item.butSelChange = true
            } else {
              item.butSelChange = false
            }
          }
        }
        if (!item.isClick) {
          item.butCanChange = false
        }
      },
      selectAllList(item) {
        item.butSelChange = true
        item.butCanChange = true
        item.isClick = 1
        for (var i = 0; i < item.type.length; i++) {
          this.$set(item.type[i], "checked", true)
        }
      },
      cancelAllList(item) {
        item.butCanChange = false
        item.butSelChange = false
        item.isClick = this.isClick
        for (var i = 0; i < item.type.length; i++) {
          this.$set(item.type[i], "checked", false)
        } 
      },
      closeModal() {
        this.isChecked = false
      }
    },
  }
</script>

<style scoped>
