<template>
  <div class="top">
    <header :class="headerHiddenClass">
      <div class="primary-nav">
        <div class="medyLogoDiv">
          <img class="medyLogo" src="../assets/common/index_logo.png" @click="showMenu()">
        </div>
        <div class="searchInputDiv">
          <img class="searchIcon" src="../assets/common/search.png">
          <input class="searchInput" type="text" name="search" placeholder="200以上の医療サイトから検索">
        </div>
      </div>
      <div class="secondary-nav">
        <ul class="cate-tab">
          <li class="cate" v-bind:class="{'tab-active': cate_index === item.id}" v-for="(item, index) in cate" :key="index" @click="toggleCate(item.id)">
            <p>{{ item.des }}</p>
          </li>
        </ul>
      </div>
    </header>
    <side-menu></side-menu>
    <div id="PageList" :class="{'page': !pageHeaderHide, 'page-hidden': pageHeaderHide}" :style="pageListHeight">
      <pull-to :top-load-method="refresh" @scroll="handleScroll" :topConfig="pageHeaderConfig">
      <ul class="article-list">
        <li class="article-item" v-for="(item, index) in articleList" :key="index" @click="gotoArticleDetail(item.id)">
          <div class="article-img" v-if="item.id!='showArticleList'">
            <img v-bind:src="item.imgUrl" v-bind:alt="item.name">
          </div>
          <div class="article-info" v-if="item.id!='showArticleList'">
            <p class="article-title">{{ item.title }}</p>
            <p class="article-site">{{ item.siteName }}</p>
          </div>
          <p class="showArticleList" v-if="item.id=='showArticleList'">もっと見る</p>
        </li>
      </ul>
      </pull-to>
    </div>
    <div v-if="shouldShowLoader" class="loader">
      <img src="../assets/common/loading.gif">
    </div>
    <div :class="{'footerTabDiv': true, 'headerHiddenFooter': pageHeaderHide}">
      <ul class="footerTab">
        <li class="footerTabItem" v-for="(item, index) in footerTabs" :key="index" @click="toggleTab(item.id)">
          <img src="../assets/common/configuration.png">
          <p>{{ item.title }}</p>
        </li>
      </ul>
    </div>
    
  </div>
</template>

<script>
import PullTo from '@/components/vue-pull-to/vue-pull-to.vue'
import SideMenu from '@/components/SideMenu.vue'
const NEWS_API_KEY = 'feb8db34ea39448db7e2cdf798595036';

export default {
  name: 'top',
  data() {
    return {
      headerHiddenClass: "auto-hide-header",
      cate_index: this.Global.currentMenuId,
      cate: [
        { id: 100, des: '総合Top' },
        { id: 200, des: '臨床Q&A' },
        { id: 300, des: '医療ニュース' },
        { id: 400, des: 'アンケート' },
        { id: 500, des: '特集・企画' },
        { id: 600, des: '医学生ナビ' },
        { id: 700, des: 'ライフスタイル' }
      ],
      footerTabs: [
        { id: 0, title: "お知らせ" },
        { id: 1, title: "ポイント" },
        { id: 2, title: "メニュー" }
      ],
      articleList: [],
      shouldShowLoader: false,
      previousTop: 0,
      isLoading: false,
      scrolling: false,
      headerHiddenClass: "auto-hide-header",
      pageListHeight: 'height:100%;',
      pageHeaderHide: false,
      pageHeaderConfig: {
        pullText:'pull to refresh',
        triggerText: 'drop to refresh',
        loadingText: 'loading',
        doneText: '',
        failText: '',
        loadedStayTime: 400,
        stayDistance: 50,
        triggerDistance: 70
      },
      listScrollTop: 0,
    }
  },
  components: {
    PullTo,
    'side-menu': SideMenu
  },
  created: function () {
    this.getArticleListFromServer(null)
  },
  mounted: function() {
    this.pageListHeight = 'height:' + (window.innerHeight - 85 - 50) + 'px;'
  },
  activated: function() {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      if (this.listScrollTop > 0) {
          document.getElementsByClassName('scroll-container')[0].scrollTop = this.listScrollTop;
          this.listScrollTop = 0;
      }
    } else {
      // 普通遷移の場合、画面初期化処理を行う
    }
    
  },
  methods: {
    handleScroll: function() {
      if(!this.scrolling) {
          this.scrolling = true;
          setTimeout(this.autoHideHeader(), 250);
      }
    },
    autoHideHeader: function() {
      var scrollDelta = 0;
      var scrollOffset = 100;
      var currentTop = document.getElementsByClassName('scroll-container')[0].scrollTop;
      if ((this.previousTop - currentTop > scrollDelta && currentTop < 100) || currentTop < 100) {
        this.headerHiddenClass = "auto-hide-header";
        this.pageListHeight = 'height:' + (window.innerHeight - 85 - 50) + 'px;'
        this.pageHeaderHide = false;
      } else if( currentTop - this.previousTop > scrollDelta && currentTop > scrollOffset) {
        this.headerHiddenClass = "auto-hide-header is-hidden";
        this.pageListHeight = 'height:' + (window.innerHeight - 41 - 50) + 'px;'
        this.pageHeaderHide = true;
      }
      this.previousTop = currentTop;
      this.scrolling = false;
    },
    toggleCate: function (index) {
      if (!this.isLoading) {
        this.cate_index = index;
        this.Global.currentMenuId = index;
        this.getArticleListFromServer(null);
      }
    },
    toggleTab: function(tabId) {
      if (tabId == 2) {
        
      } else if (tabId == 0) {
        
      } else if (tabId == 1) {
         
      }
    },
    refresh: function(loaded) {
      this.getArticleListFromServer(loaded);
    },
    getArticleListFromServer: function(loaded) {
      this.shouldShowLoader = true;
      this.isLoading = true;
      var currentCategory = null;
      switch(this.cate_index) {
        case this.Global.MENU_ID_CLINIC:
          currentCategory = 'business';
          break;
        case this.Global.MENU_ID_NEWS:
          currentCategory = 'entertainment';
          break;
        case this.Global.MENU_ID_ENQUETE:
          currentCategory = 'health';
          break;
        case this.Global.MENU_ID_FEATURE:
          currentCategory = 'science';
          break;
        case this.Global.MENU_ID_NAVI:
          currentCategory = 'sports';
          break;
        case this.Global.MENU_ID_LIFE:
          currentCategory = 'technology';
          break;
        default:
          break;
      }
      var requestUrl = currentCategory != null ? process.env.VUE_APP_API_ENDPOINT + '/v2/top-headlines?country=jp&category=' + currentCategory : process.env.VUE_APP_API_ENDPOINT + '/v2/top-headlines?country=jp';
      requestUrl = requestUrl + '&apiKey=' + NEWS_API_KEY;
      var _this = this;
      this.$axios.get(requestUrl)
        .then(function (response) {
            _this.articleList = [];
            var topList = response.data.articles;
            for(var i = 0; i < topList.length; i++) {
                var newsInfo = topList[i];
                var articleInfo = {
                    id: 1000 + i,
                    title: newsInfo.title,
                    siteName: newsInfo.author != null ? newsInfo.author : "",
                    openDate: newsInfo.publishedAt != null ? newsInfo.publishedAt : "",
                    imgUrl: newsInfo.urlToImage != null ? newsInfo.urlToImage : "",
                    description: newsInfo.description != null ? newsInfo.description : ""
                };
                _this.articleList.push(articleInfo);
            }
            if (_this.articleList.length > 0) {
                var articleInfo = {
                    id: 'showArticleList'
                };
                _this.articleList.push(articleInfo);
            }
            _this.$toasted.show('記事一覧更新完了');
        })
        .catch(function (error) {
            _this.$toasted.show('記事一覧更新失敗');
        })
        .then(function () {
            _this.shouldShowLoader = false;
            _this.isLoading = false;
            if (typeof(loaded) != 'undefined' && loaded != null) {
                loaded('done');
            }
        });
    },
    gotoArticleDetail: function(id) {
      this.listScrollTop = document.getElementsByClassName('scroll-container')[0].scrollTop;
      if (id == 'showArticleList') {
        this.gotoArticleList();
      } else {
        this.$router.push({ name: 'articleDetail', params: { articleId: id} });
      }
      
    },
    gotoArticleList: function() {
      this.$router.push({ name: 'articleList', params: { menuId: 100} });
    },
    showMenu: function() {
      this.$store.dispatch('showSideMenu');
    }
  }
}
</script>

<style>
  .top h1, ul,li,p {
    margin: 0;
    padding: 0;
  }
  .top li {
    list-style: none;
  }
  .top .cate {
    cursor: pointer;
    display: inline-block;
    min-width: 100px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 16px;
    color: #2B2B2B;
    word-wrap: break-word;
    font-weight: 500;
    box-shadow: 1px -2px 3px 0px rgba(0,0,0,0.2);
  }
  .top .cate-tab {
    white-space: nowrap;
    overflow-x: scroll;
    -webkit-overflow-scrolling: touch;
    background-color: #D3D3D3;
  }
  .cate.tab-active {
    background-color: #FFFFFF;
    border-radius: 5px 5px 0px 0px;
  }
  .cate p {
    padding: 0px 5px;
  }
  .page {
    margin-top: 84px;
  }
  .page-hidden {
    -webkit-transform: translateY(41px);
    -ms-transform: translateY(41px);
    transform: translateY(41px);
  }
  .page {
    margin-top: 84px;
  }
  .page-hidden {
    -webkit-transform: translateY(41px);
    -ms-transform: translateY(41px);
    transform: translateY(41px);
  }
  .article-item {
    display: flex;
    padding:3px;
    height: 100px;
    background: #fff;
    border-bottom: 1px solid #7D7D7D;
    overflow: hidden;
    cursor: pointer;
  }
  .article-item:hover .article-img {
    filter:alpha(opacity=80);
    opacity:0.8;
  }
  .article-item:hover .article-title {
    color: #20BCC4;
  }
  .article-item .showArticleList {
    width: 100%;
    height: 100px;
    line-height: 100px;
    text-align: center;
    font-size: 16px;
  }
  .article-img {
    width: 120px;
    height: 90px;
    position: relative;
    margin: 5px 10px 5px 5px;
    display: block;
    overflow: hidden;
    box-shadow: 1px 1px 8px 0px rgba(0,0,0,0.3);
  }
  .article-img img {
    top: 0;
    left: 0;
    width: 120px;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
  }
  .article-info {
    width: calc(100% - 141px);
  }
  .article-title {
    width: 100%;
    height: 38px;
    font-size: 16px;
    color: #2B2B2B;
    word-wrap: break-word;
    font-weight: 700;
    line-height: 19px;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    margin: 5px 0px 15px 0px;
  }
  .article-site {
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;
    color: #A1A1A1;
  }
  .medyLogoDiv {
    display: inline-block;
    height: 44px;
  }
  .medyLogoDiv .medyLogo {
    width: 58px;
    margin: 15px 0px 0px 10px;
  }
  .searchInputDiv {
    width: calc(100% - 68px);
    height: 44px;
    display: inline-block;
    vertical-align: top;
  }
  .searchInputDiv .searchIcon {
    width: 16px;
    margin: 15px 0px 0px 16px;
    position: absolute;
    cursor: pointer;
  }
  .searchInputDiv .searchInput {
    width: 200px;
    height: 26px;
    margin: 0px 0px 0px 10px;
    display: inline-block;
    vertical-align: middle;
    border-width: 1px;
    border-radius: 4px;
    border-color: #20BCC4;
    border-style: solid;
    padding-left: 5px;
    font-size: 14px;
    font-weight: 400;
    color: #20BCC4;
    padding-left: 30px;
  }
  .loader {
    width: 100%;
    height: 100px;
  }
  .loader img {
    width: 30px;
    margin: 35px calc((100% - 30px) / 2);
  }
  .footerTabDiv {
    width: 100%;
    height: 44px;
    margin-top: 8px;
    box-shadow: 0px -2px 3px 0px rgba(0,0,0,0.2);
  }
  .headerHiddenFooter {
    margin-top: 48px;
  }
  .footerTabDiv .footerTab {
    width: 100%;
    height: 44px;
    white-space: nowrap;
    background-color: #D3D3D3;
  }
  .footerTabDiv .footerTab .footerTabItem {
    width: calc(100% / 3);
    display: inline-block;
    height: 44px;
    text-align: center;
    box-shadow: 1px 0px 3px 0px rgba(0,0,0,0.2);
    cursor: pointer;
  }
  .footerTabDiv .footerTab .footerTabItem img {
    width: 20px;
  }
  .footerTabDiv .footerTab .footerTabItem p {
    font-size: 12px;
    color: #2B2B2B;
    word-wrap: break-word;
    font-weight: 400;
    line-height: 14px;
  }
  .auto-hide-header {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 44px;
    line-height: 44px;
    background-color: #ffffff;
    border-bottom: 1px solid #ddd;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    will-change: transform;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
  }
  .auto-hide-header::after {
    clear: both;
    content: "";
    display: block;
  }
  .auto-hide-header.is-hidden {
    -webkit-transform: translateY(-100%);
    -ms-transform: translateY(-100%);
    transform: translateY(-100%);
  }
  .primary-nav {
    height: 44px;
  }
  .secondary-nav {
    position: relative;
    z-index: 1;
    height: 40px;
    overflow: hidden;
    clear: both;
    background-color: #25283D;
    border-bottom: 1px solid #EEAD0E;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    will-change: transform;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
  }
</style>
