        {{title}}
      </div>
    </div>
    <header class="searchHeader" :class="{tabHeader:titleFlag}">
      <div class="searchBar">
        <svg
          class="searchIcon"
          @click="doit()"
          xmlns="http://www.w3.org/2000/svg"
          width="24"
          height="24"
          viewBox="0 0 24 24">
          <path fill="#333" fill-rule="evenodd" d="M20.842 20.033a.537.537 0 0 1 .158.396c0 .158-.053.29-.158.395a.553.553 0 0 1-.403.176.553.553 0 0 1-.403-.176l-5.433-5.484c-.62.539-1.32.96-2.103 1.265a6.8 6.8 0 0 1-2.49.457 6.81 6.81 0 0 1-2.733-.553 7.185 7.185 0 0 1-2.235-1.503 6.91 6.91 0 0 1-1.499-2.233A6.96 6.96 0 0 1 3 10.031c0-.972.181-1.886.543-2.742a6.91 6.91 0 0 1 1.499-2.232 7.185 7.185 0 0 1 2.235-1.503A6.81 6.81 0 0 1 10.01 3c.958 0 1.863.185 2.716.554a7.028 7.028 0 0 1 3.742 3.735c.368.856.552 1.77.552 2.742 0 .856-.146 1.667-.438 2.435a7.304 7.304 0 0 1-1.192 2.065l5.451 5.502zm-10.831-4.078c.806 0 1.568-.155 2.287-.466a5.882 5.882 0 0 0 1.875-1.274 6.11 6.11 0 0 0 1.262-1.881 5.73 5.73 0 0 0 .465-2.303 5.73 5.73 0 0 0-.465-2.302 6.11 6.11 0 0 0-1.262-1.881 5.882 5.882 0 0 0-1.875-1.275 5.704 5.704 0 0 0-2.287-.466c-.818 0-1.586.156-2.305.466A5.882 5.882 0 0 0 5.83 5.848a6.11 6.11 0 0 0-1.262 1.88 5.73 5.73 0 0 0-.465 2.303c0 .82.155 1.588.465 2.303a6.11 6.11 0 0 0 1.262 1.88c.531.54 1.156.965 1.875 1.275.719.31 1.487.466 2.305.466z"/>
        </svg>
        <div class="searchHolder">
          <input id="searchText" class="inputStyle"  type="search" ref="inputText" @keyup.enter='doit()' @input="valueChange()"/>
          <div class="keyWord" @click="getFocus()">{{placeholder}}</div>
        </div>
        <svg
          @click="clear()"
          id="delete"
          class="deleteIcon"
          v-if="clearFlag"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 24 24"
          fill="#333333"
          stroke="currentColor"
          stroke-width="3"
          stroke-linecap="round"
          stroke-linejoin="round">
          <circle cx="12" cy="12" r="10" style="color:#333333"></circle>
          <line x1="17" y1="7" x2="7" y2="17" style="color:#ffffff"></line>
          <line x1="7" y1="7" x2="17" y2="17" style="color:#ffffff"></line>
        </svg>
      </div>
    </header>
    <div class="headerSpace" :class="{tabSpace:titleFlag}"></div>
    <div>
      <div class="outputArea" v-if="remindFlag">
        <p id="holder" class="outputPlaceHolder">{{siteLable}}{{lable}}</p>
      </div>
      <div class="listStyle" ref="listStyle">
        <pull-to :is-top-bounce="false" :is-bottom-bounce="false" @scroll="handleScroll">
          <div ref="scrollList">
            <SearchList v-if="searchList" ref="searchList"/>
            <div id="moreNews" class="more">
              <div v-if="refreshFlag">
                <svg
                  v-if="refLoading"
                  @click="refChange()"
                  class="refreshIcon"
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  viewBox="0 0 16 16">
                  <g fill="none" fill-rule="evenodd">
                    <path d="M0 0h16v16H0z"/>
                    <path fill="#000" d="M12.691 7.922a4.354 4.354 0 0 1-.283 2.412c-.221.52-.534.983-.937 1.387-.404.403-.87.716-1.397.937A4.33 4.33 0 0 1 8.375 13a4.33 4.33 0 0 1-1.7-.342 4.322 4.322 0 0 1-1.396-.937 4.322 4.322 0 0 1-.937-1.397A4.33 4.33 0 0 1 4 8.625c0-.592.114-1.159.342-1.7.221-.527.534-.992.937-1.396.404-.403.87-.716 1.397-.937a4.451 4.451 0 0 1 1.67-.342L7.7 3.596a.316.316 0 0 1-.107-.244c0-.098.034-.181.102-.25A.339.339 0 0 1 7.946 3c.097 0 .182.036.253.107l1.319 1.319a.347.347 0 0 1 .107.254.316.316 0 0 1-.107.244L8.199 6.252a.347.347 0 0 1-.503 0 .35.35 0 0 1 .005-.498l.801-.8h-.127c-.488 0-.957.092-1.406.277-.45.186-.847.451-1.192.796-.345.345-.61.743-.796 1.192-.185.449-.278.918-.278 1.406 0 .488.093.957.278 1.406.186.45.451.847.796 1.192.345.345.743.61 1.192.796.449.185.918.278 1.406.278.488 0 .957-.093 1.406-.278.45-.186.847-.451 1.192-.796a3.642 3.642 0 0 0 1.025-3.184.358.358 0 0 1 .288-.405.348.348 0 0 1 .264.058.317.317 0 0 1 .141.23z"/>
                  </g>
                </svg>
                <div class="loading" v-if="loadings">{{loading}}</div>
                <div class="refresh" v-if="refLoading" @click="refChange()">{{refresh}}</div>
              </div>
            </div> 
          </div>
        </pull-to>
      </div>
    </div>
    <MedyFooter v-show="!titleFlag"/>
    <CommonModal :show="isChecked" :patternName="modalPattern" @btnFunction1="closeModal()"/>
  </div>
</template>

<script>
import SearchList from "@/components/cmptsClinical/SearchList.vue"
import CommonModal from '@/components/cmptsModal/CommonModal.vue'
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
import MedyFooter from '@/components/MedyFooter.vue'


export default {
  name: 'search',
  components: {
    SearchList,
    MedyFooter,
    CommonModal,
    PullTo
  },
  data() {
    return {
        titleFlag: false,
        title: '',
//        pageDivCss: '',
        clearFlag: false,
        searchList: false,
        placeholder: "キーワード検索",
        lable: '記事を検索します。',
        siteLable: '入力されたキーワードを内容に含む',
        refresh: '再読み込み',
        remindFlag: true,
        refreshFlag: false,
        isChecked: false,
        isloading: false,
        modalPattern: 'keyWord',
        searchResult: '検索結果は0件でした。',
        loading: 'loading…',
        refLoading: true,
        loadings: false
    }
  },
  activated: function () {
    this.init();
  },
  methods: {
    init() {
//      this.pageDivCss = 'height:' + (window.innerHeight - 42 - 46) + 'px;margin-bottom:46px;';
      this.clear()
      if (this.$route.params.tabId != 0) {
        this.$refs.listStyle.style.height = (window.innerHeight - 42 - 36) + 'px';
        this.titleFlag = true
        if (this.$route.params.tabId == 1) {
          this.title = "臨床Q&A"
        } else if (this.$route.params.tabId == 2) {
          this.title = "ライフスタイルQ&A"
        } else if (this.$route.params.tabId == 3) {
          this.title = "ちょっとひと息"
        } else if (this.$route.params.tabId == 4) {
          this.title = "アンケート結果記事検索"
        } else if (this.$route.params.tabId == 5) {
          this.title = "Doctor's Library"
        } else if(this.$route.params.tabId == 6){
          this.title = "Opinions"
        }
        this.siteLable = this.title
      } else {
        this.$refs.listStyle.style.height = (window.innerHeight - 42 - 36) + 'px';
        this.titleFlag = false
        this.siteLable = '入力されたキーワードを内容に含む'
      }
    },

    goBack: function () {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },

    valueChange() {
      if (this.$refs.inputText.value != '') {
        this.clearFlag = true
        this.placeholder = ''
      } else {
        this.clearFlag = false
        this.placeholder = 'キーワード検索'
      }
    },

    clear: function () {
      this.$refs.inputText.value = ''
      this.remindFlag = true
      this.searchList = false
      this.refreshFlag = false
      this.clearFlag = false
      this.siteLable = '入力されたキーワードを内容に含む'
      this.lable = '記事を検索します'
      this.placeholder = 'キーワード検索'
    },

    doit: function () {
      if (this.$refs.inputText.value != "") {
        if (this.$refs.inputText.value == '000') {
          this.siteLable = ''
          this.lable = this.searchResult
          this.searchList = false
          this.remindFlag = true
          this.refreshFlag = false
        } else {
          this.clearFlag = true
          this.searchList = true
          this.remindFlag = false
          this.refreshFlag = true
        }
      } else {
        this.isChecked = true
      }
    },
    handleScroll() {
      var _this = this
      var allHeight = this.$refs.scrollList.scrollHeight
      var needRefreshHeight = allHeight - window.innerHeight
      var scrollTop = document.getElementsByClassName("scroll-container")[0].scrollTop
      if (scrollTop >= needRefreshHeight && !this.refLoading) {
        setTimeout(function() {
          _this.$refs.searchList.addOutput()
        }, 800);
      }
    },
    refChange() {
      this.refLoading = false
      this.loadings = true
      this.handleScroll()
    },
    closeModal() {
      this.isChecked = false
    },
    getFocus () {
      this.$refs.inputText.focus()
    }
  }
}
</script>

