<template>
  <div :class="['article-pickup', (isNoData || isLoadingFail) ? 'specialHeightCol' : '']">
    <div class="pickupTitle">
      <span>{{title}}</span>
    </div>
    <div class="pickupSort">
      <span :class="activeTab == 'day' ? 'active' : '' " @click="switchTab('day')">昨日</span>
      <div class="split"></div>
      <span :class="activeTab == 'week' ? 'active' : '' " @click="switchTab('week')">週間</span>
      <div class="split"></div>
      <span :class="activeTab == 'comment' ? 'active' : '' " @click="switchTab('comment')">コメント数</span>
    </div>
    <div class="pickupArticleList">
      <pickUp-article :article="item" :sortNo="index + 1" v-for="(item, index) in articles" :key="index" @setListScrollTop="setListScrollTop"/>
      <div :class="['special', isNoData? 'left' : '']" v-if="(isNoData || isLoadingFail) && !isLoading">
        <div class="clickArea" @click="reLoadData">
          <div class="refresh" v-if="isLoadingFail">
            <img src="@/assets/medyImages/refresh-small.png" srcset="@/assets/medyImages/refresh-small@2x.png 2x, @/assets/medyImages/refresh-small@3x.png 3x">
          </div>
          <span>{{specialText}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PickUpArticle from "@/components/cmptsCommon/PickUpArticle.vue"

export default {
  name: 'pickUpArticles',
  props: {
    menuId: Number
  },
  data() {
    return {
      activeTab: 'day',
      articles: [],
      title: '',
      isLoading: false,
      isLoadingFail: false,
      isNoData: false,
      specialText: 'コンテンツがありません。',
      fakeArticles: [
        {
          articleId: "310001",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
        }
      ]
    };
  },
  components: {
    "pickUp-article": PickUpArticle
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      //TODO AJAX
      var _this = this;
      _this.articles = [];
      _this.isNoData = false;
      _this.isLoadingFail = false;
      _this.specialText = 'コンテンツがありません。';
      _this.activeTab = 'day';
      if(_this.menuId == _this.Global.MENU_ID_CLINIC){
        _this.title='臨床Q&A PICK UP';
      } else if(_this.menuId == _this.Global.MENU_ID_NEWS){
        _this.title='医療ニュースランキング';
      }
      this.getArticleByTab();
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.$emit("setListScrollTop");
    },
    switchTab: function (chooseTab) {
      // タブを切り替え
      if (this.isLoading || this.activeTab == chooseTab) {
        return;
      }
      this.isLoading = true;
      if (chooseTab == 'day' && this.activeTab != 'day') {
        this.activeTab = 'day';
      } else if (chooseTab == 'week' && this.activeTab != 'week') {
        this.activeTab = 'week';
      } else if (chooseTab == 'comment' && this.activeTab != 'comment') {
        this.activeTab = 'comment';
      }
      this.getArticleByTab();
    },
    getArticleByTab: function (loaded = null) {
      var _this = this;
      if(loaded == null){
        _this.articles = [];
      }
      //TODO AJAX
      setTimeout(function () {
        //TODO DELETE
         if(_this.activeTab == 'week'){
           _this.specialText = 'コンテンツがありません。';
          _this.isNoData = true;
          _this.isLoadingFail = false;
        } else if(_this.activeTab == 'day'){
          _this.isNoData = false;
          _this.isLoadingFail = false;
          _this.articles = _this.fakeArticles;
        } else {
          _this.specialText = '再読み込み';
          _this.isLoadingFail = true;
          _this.isNoData = false;
        }
        _this.isLoading = false;
      }, 1000);
    },
    reLoadData: function(){
      if(!this.isLoadingFail){
        return;
      }
      this.isLoading = true;
      this.getArticleByTab();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.article-pickup {
  width: 100%;
  height: 454px;
  background-color: #fafafa;
}

.article-pickup.specialHeightCol{
  height: auto;
}

.article-pickup .pickupTitle {
  padding-top: 20px;
  padding-left: 0px;
  height: 36px;
  line-height: 36px;
}

.article-pickup .pickupTitle span {
  padding-top: 0px;
  padding-left: 12px;
  font-family: 'Noto Sans JP', sans-serif;
  font-size: 18px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: var(--black);
  height: 27px;
}

.article-pickup .pickupSort {
  float: right;
  margin-right: 12px;
  height: 24px;
  margin-top: 5px;
}

.article-pickup .pickupSort span {
  margin-top: 2px;
  width: 60px;
  height: 18px;
  font-size: 12px;
  font-weight: 300;
  color: var(--brown-grey);
}

.article-pickup .pickupSort .split {
  width: 1px;
  height: 8px;
  background-color: var(--black);
  margin-left: 10px;
  margin-right: 10px;
  display: inline-block;
}

.article-pickup .pickupSort .active {
  color: var(--black);
}

.article-pickup .pickupArticleList {
  position: relative;
  display: inline-block;
  width: 100%;
  min-height: 52px;
  vertical-align: top;
}

.article-pickup .special{
  text-align: center;
  margin-top: 16px;
  margin-bottom: 16px;
  height: 20px;
}

.article-pickup .special.left{
  text-align: left;
  padding-left: 12px;
}

.article-pickup .special span{
  font-size: 14px;
  height: 20px;
  color: var(--black);
  line-height: 20px;
  vertical-align: top;
  display: inline-block;
}

.article-pickup .special .refresh{
  width: 16px;
  height: 16px;
  margin-right: 8px;
  padding-top: 2px;
  display: inline-block;
}

.article-pickup .special .clickArea{
  display: inline-block;
}
</style>
