<template>
	<div class="comment" id="comment_top">
        <CommonModal :show="showMessage" :patternName="sendCommentModal" @close="modalClose" @btnFunction1="modalClose" />
        <div class="title">
            <span class="text largeFont" v-if="!haveMyComment()" > コメントする </span>
            <span class="text largeFont" v-if="haveMyComment()" > 自分のコメント </span> 
            <span class="number">
                <a id="commentSize">{{myComment==null? 0:myComment.commentDetail.length}}</a>/1000 
            </span>
        </div>
        <textarea id="commentTextArea" class="commentTextArea" cols="100" @keyup="changeComment"
            rows="4" placeholder="コメントを入力してください。（任意）２０文字以上" maxlength="1000" :value="getCommentDetail()" ></textarea>
        <div class="notice_1">
            <a style="color: #0076ff;text-decoration:underline" @click="gotoRule()">利用規約</a>を順守し、内容に責任をもってご投稿ください。
        </div>
        <button class="consentRule" v-if="!haveMyComment()" @click="sendComment()">
            <span class="fontStyle">規約に同意してコメントする</span>
        </button>
        <button class="myCommentBtn" v-if="haveMyComment()" @click="updateComment()">
            <span class="fontStyle">更新する</span>
        </button>
        <span class="cancelLink" v-if="haveMyComment()" @click="cancelComment()">編集をキャンセル</span>
        <div class="notice_2" @click="showNotice">
            <span class="fontStyle"> コメント投稿可能なMEDY会員の方は以下の通りです。 </span> 
            <img src="@/assets/common/up.png" v-show="shouldShowNotice" >
            <img src="@/assets/common/down.png" v-show="!shouldShowNotice" >
        </div>
        <div class="notice_3" v-show="shouldShowNotice">Advisory Doctor 医師 薬剤師 医学生 看護師 医学研究者 医療事務
            臨床検査技師 理学療法士 診療放射線技師 獣医師 臨床工学技士 看護学生 保健師 言語聴覚士 作業療法士 薬学生 管理栄養士
            助産師歯学生 歯科医師 その他医療関係者</div>
	</div>	
</template>

<script>
import CommonModal from "@/components/cmptsModal/CommonModal.vue"

export default {
  name: "QADetailTop",
  props: {
        myComment:Object,
  },
  data() {
    return {
      shouldShowNotice:false,
      showMessage:false,
      sendCommentModal:"sendComment",

      articleInfo:{
        nMinute:"2018年11月21日",
        site:"MEDY臨床相談",
        title:"臨床Q&amp;A Vol.363「テリパラチドを2年使用した患者の新規圧迫骨折の治タイトル全部表示",
        image:"http://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
        question:"テリパラチドを2年使用した患者の新規圧迫骨折の治療について",
        job:"病院の勤務医整形外科 60代",
        answer:"テリパラチドはもう使用出来ないですが 皆さんは次に投与する薬剤は何をお使いですか？"
      }
    };
  },
  created: function () {
  },
  mounted: function () {
  },
  activated: function () {
      this.shouldShowNotice = false;
      this.showMessage = false;
  },
  components: {
    CommonModal
  },
  methods: {
    showMask: function (event) {
      this.$emit('showMask', event);
    },
    haveMyComment: function(){
        if(this.myComment != null && this.myComment !=undefined && this.myComment != "" ){
            return true;
        }
        return false;
    },
    showNotice: function(){
        this.shouldShowNotice = !this.shouldShowNotice;
    },
    getCommentDetail: function(){
        if(this.haveMyComment()){
            return this.myComment.commentDetail;
        }
        return "";
    },
    getCommentSize: function(){
        return document.getElementById("commentTextArea").value.length;
    },
    changeComment: function(){
        document.getElementById("commentSize").text = document.getElementById("commentTextArea").value.length;
    },
    updateComment:function(){
        var comment = document.getElementById("commentTextArea").value;
        var _this = this;
        _this.$emit("updateComment", comment);

        _this.showMessage = true;
        setTimeout(() => {
            _this.showMessage = false;
            _this.$emit("hideComment");
        }, 600);
    },
    cancelComment:function(){
        document.getElementById("commentTextArea").value = this.getCommentDetail();
        this.changeComment();
        this.$emit("hideComment");
    },
    sendComment:function(){
        var _this = this;
        var comment = document.getElementById("commentTextArea").value;
        _this.$emit("sendComment", comment);

        _this.showMessage = true;
        setTimeout(() => {
            _this.showMessage = false;
            _this.$emit("hideComment");
        }, 600);

    },
    modalClose:function(){
        this.showMessage = false;
    },
    gotoRule:function(){
        window.open("https://content.medy-id.jp/comment-tos.html");
    }
  }
};
</script>

<style scope>
