<template>
  <header :class="['trans-header', pageHeaderHide ? 'is-hidden' : '']">
    <div class="header">
      <div class="topDiv">
        <!-- menu -->
        <img
          class="menu"
          src="../assets/medyImages/icon-navbar-menu.png"
          srcset="../assets/medyImages/icon-navbar-menu@2x.png 2x, ../assets/medyImages/icon-navbar-menu@3x.png 3x"
          @click="showMenu()"
        >
        <!-- logo -->
        <img
          class="logo"
          src="../assets/medyImages/logo.png"
        >
        <!-- notice -->
        <img
          class="notice"
          @click="goToNotice"
          src="../assets/medyImages/icon-navbar-notice.png"
          srcset="../assets/medyImages/icon-navbar-notice@2x.png 2x, ../assets/medyImages/icon-navbar-notice@3x.png 3x"
        >
      </div>

      <div class="navDiv" ref="navDivCol">
        <div class="leftBar"></div>

        <div class="rightBar"></div>

        <div class="navWidCol" ref="navWidCol">
          <div
            ref="headerItems"
            v-for="(item, index) in menuInfos"
            :key="index"
            :class="['headerItem',(index == menuInfos.length -1) ? 'lastMenu' : '', (activeMenu == item.menuId) ? 'active' : '']"
            @click="switchMenu(item.menuId, index)"
          >{{item.menuName}}</div>
        </div>
        <div class="chooseMark" ref="chooseMark"></div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: "medyHeader",
  props: {
    pageHeaderHide: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeMenu: this.Global.currentMenuId,
      menuInfos: [
        { menuId: 200, menuName: "臨床Q&A" },
        { menuId: 300, menuName: "医療ニュース" },
        { menuId: 400, menuName: "アンケート" },
        { menuId: 500, menuName: "特集・企画" },
        { menuId: 600, menuName: "医学生ナビ" },
        { menuId: 700, menuName: "ライフスタイル" }
      ]
    };
  },
  components: {
  },
  mounted: function () {
    this.init();
  },
  activated: function(){
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      let _this = this;
      let navWid = 0;
      let menuIndex = 0;
      _this.menuInfos.forEach(function (menuItem, itemIndex) {
        if (menuItem.menuId == _this.activeMenu) {
          menuIndex = itemIndex;
          return false;
        }
      });

      _this.$refs.headerItems.forEach(function (item, index) {
        navWid += item.offsetWidth + 18;
      });

      _this.$refs.navWidCol.style.width = navWid + "px";
      _this.moveChooseMark(menuIndex);
      _this.$refs.chooseMark.style.transition = "all 250ms ease-in-out";
    },
    showMenu: function () {
      this.$store.dispatch("showSideMenu");
    },
    switchMenu: function (menuId, index) {
      if(this.activeMenu == menuId){
        return;
      }
      this.activeMenu = menuId;
      this.Global.currentMenuId = menuId;
      this.$emit("update:activeMenu", menuId);
      this.moveChooseMark(index);
    },
    moveChooseMark: function (index) {
      let moveLen = 0;
      this.$refs.headerItems.forEach(function (item, itemIndex) {
        if (itemIndex > index) {
          return false;
        } else if (itemIndex == index) {
          moveLen += item.offsetWidth / 2;
        } else {
          moveLen += item.offsetWidth;
        }
      })
      moveLen += 18 * index;
      moveLen -= 4;
      this.$refs.chooseMark.style.marginLeft = moveLen + 'px';

      let cenPos = window.innerWidth / 2;
      if (moveLen - cenPos <= 0) {
        this.$refs.navDivCol.scrollLeft = 0;
      } else if (moveLen - cenPos > parseInt(this.$refs.navWidCol.style.width) - window.innerWidth) {
        this.$refs.navDivCol.scrollLeft = parseInt(this.$refs.navWidCol.style.width) - window.innerWidth;
      } else {
        this.$refs.navDivCol.scrollLeft = moveLen - cenPos + 12;
      }
    },
    goToNotice:function(){
      this.$emit("goToNotice");
    }
  }
};
</script>

<style scope>
header {
  position: fixed;
  top: 0;
  left: 0px;
  bottom: 0px;
  width: 100%;
  height: 84px;
  background-color: var(--white-two);
  z-index: 8;
}

.header .topDiv {
  position: relative;
  width: 100%;
  height: 42px;
  background-color: var(--white-two);
}

.header .topDiv .menu {
  position: absolute;
  left: 12px;
  top: 9px;
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.header .topDiv .logo {
  position: absolute;
  left: calc((100% - 81px) / 2);
  top: 9px;
  width: 81px;
  height: 24px;
  object-fit: contain;
}

.header .topDiv .notice {
  position: absolute;
  right: 12px;
  top: 9px;
  width: 24px;
  height: 24px;
  object-fit: contain;
}

.header .navDiv {
  width: 100%;
  height: 42px;
  box-shadow: inset 0 -1px 0 0 var(--white-three);
  background-color: var(--white-two);
  position: fixed;
  overflow-x: auto;
  overflow-y: hidden;
  z-index: 1;
}

.header .navDiv .leftBar {
  position: fixed;
  top: 42px;
  left: 0px;
  width: 15px;
  height: 41px;
  background-image: linear-gradient(to left, rgba(255, 255, 255, 0), var(--white-two));
  z-index: 10;
}

.header .navDiv .rightBar {
  position: fixed;
  top: 42px;
  right: 0px;
  width: 15px;
  height: 41px;
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0), var(--white-two));
  z-index: 15;
}
.header .navDiv .navWidCol {
  position: absolute;
  overflow-y: hidden;
  width: 700px;
}

.header .navDiv .chooseMark {
  position: absolute;
  margin-left: 30px;
  margin-top: 31px;
  margin-bottom: 8px;
  width: 21px;
  height: 3px;
  border-radius: 1.5px;
  background-color: var(--tealish);
}

.header .navDiv .headerItem {
  position: relative;
  margin-right: 18px;
  margin-top: 6px;
  height: 20px;
  font-size: 14px;
  font-weight: 300;
  color: var(--black);
  padding-left: 12px;
  display: inline-block;
}

.header .navDiv .active {
  color: var(--tealish);
}

.header .navDiv .lastMenu {
  margin-right: 12px;
}

.trans-header.is-hidden {
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.trans-header {
  background-color: var(--white-two);
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  will-change: transform;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}
.trans-header::after {
  clear: both;
  content: "";
  display: block;
}
</style>