<template>
  <div class="clinicalQA">
    <GenresModal :menuGenres="clinicalGenre" v-show="isGenreVisible" @close="toggleArticleGenre(false)" @changeGenre="changeGenre" @changeLoadingFlag="changeLoadingFlag"/>
    <div class="clinicalHeaderCol">
      <div class="clinicalHeader" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'">
        <div class="back" @click="goBack()">
          <img src="@/assets/medyImages/icon-navbar-back.png" srcset="@/assets/medyImages/icon-navbar-back@2x.png 2x, @/assets/medyImages/icon-navbar-back@3x.png 3x"/>
        </div>

        <div class="qaTitle">
          <span class="qaText">臨床Q&amp;A</span>
        </div>

        <div class="search" @click="goSearch()">
          <img src="@/assets/medyImages/search-white.png" srcset="@/assets/medyImages/search-white@2x.png 2x, @/assets/medyImages/search-white@3x.png 3x"/>
        </div>

        <div class="rightMenu" @click="toggleArticleGenre(true)">
          <img src="@/assets/medyImages/24-contents-sorttag.png" srcset="@/assets/medyImages/24-contents-sorttag@2x.png 2x, @/assets/medyImages/24-contents-sorttag@3x.png 3x"/>
        </div>
      </div>
    </div>
    <div class="clinicalPullCol">
      <pull-to ref="clinicalListPull" @scroll="scrollForLoad" :is-bottom-bounce="false" :top-load-method="refresh" :topConfig="pageHeaderConfig">
        <div class="clinicalQATopArea">
          <div class="topImgCol" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'"></div>
          <div class="clinicalQATop">
            <div class="clinicalQATitleArea">
              <div class="introduceAreaTitle">医者ユーザー4万人と話し合おう</div>
              <div class="introduceAreaDetail">臨床の場でふと感じた疑問や不安。<br/>他の医師の考えや対処を知ることが、次への一歩に繋がる。</div>
            </div>
            <div class="questionBtn">
              <span class="question">質問を投稿する</span>
            </div>
          </div>
          <div class="labelCol">
            <div class="labelArea">  
              <div class="labelImg">
                <img src="@/assets/medyImages/16-contents-sorttag-small.png" srcset="@/assets/medyImages/16-contents-sorttag-small@2x.png 2x, @/assets/medyImages/16-contents-sorttag-small@3x.png 3x"/>
              </div>
              <span v-if="currentGenreId != 0">ジャンルで絞り込む :&nbsp;</span>
              <span>{{currentGenreName}}</span>
            </div>
          </div>
        </div>
        <div class="articleListCol" ref="articleListCol">
          <ArticleList ref="articleListClinical" :menuId="Global.MENU_ID_CLINIC" :isTop="false" @setListScrollTop="setListScrollTop" @changeLoadingFlag="changeLoadingFlag"/>
        </div>
      </pull-to>
    </div>
  <GoTopMark @goTop="goTop" v-if="isTopMarkShow"/>
  </div>
</template>

<script>
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"
import GenresModal from "@/components/cmptsModal/GenresModal.vue"
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
import GoTopMark from "@/components/cmptsCommon/GoTopMark.vue"
import BackGroundImg from "@/assets/medyImages/clinicalList_background.png"

export default {
  name: 'clinicalList',
  components: {
    ArticleList,
    GenresModal,
    GoTopMark,
    PullTo
  },
  data: function () {
    return {
      isCheckIn: true,
      isGenreVisible: false,
      currentGenreName: 'すべて',
      currentGenreId: 0,
      clinicalGenre: [
        {
          genreId: 0,
          genreName: 'すべて'
        }
      ],
      fakeGenreData: [
        {
          genreId: 1,
          genreName: '循環器'
        },
        {
          genreId: 2,
          genreName: '血液内科'
        },
        {
          genreId: 3,
          genreName: '記事分類3'
        },
        {
          genreId: 4,
          genreName: '記事分類4'
        },
        {
          genreId: 5,
          genreName: '記事分類5'
        },
        {
          genreId: 6,
          genreName: '記事分類6'
        },
        {
          genreId: 7,
          genreName: '記事分類7'
        },
        {
          genreId: 8,
          genreName: '記事分類8'
        },
        {
          genreId: 9,
          genreName: '記事分類9'
        },
        {
          genreId: 10,
          genreName: '記事分類10'
        },
        {
          genreId: 11,
          genreName: '記事分類11'
        },
        {
          genreId: 12,
          genreName: '記事分類12'
        },
        {
          genreId: 13,
          genreName: '記事分類13'
        },
        {
          genreId: 14,
          genreName: '記事分類14'
        },
        {
          genreId: 15,
          genreName: '記事分類15'
        },
        {
          genreId: 16,
          genreName: '記事分類16'
        },
        {
          genreId: 17,
          genreName: '記事分類17'
        }
      ],
      pageHeaderConfig: {
        pullText: 'リフレッシュ',
        triggerText: 'リフレッシュ',
        loadingText: 'リフレッシュ中',
        doneText: '',
        failText: '',
        loadedStayTime: 400,
        stayDistance: 50,
        triggerDistance: 70
      },
      listScrollTop: 0,
      isArticleLoading: false,
      backGroundImg: BackGroundImg,
      isTopMarkShow: false,
      tabId: 1,
    }
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      if (this.listScrollTop > 0) {
        this.$refs.clinicalListPull.setBackScrollPos(this.listScrollTop);
        this.listScrollTop = 0;
      }
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      //TODO AJAX GET CHECKIN
      this.isCheckIn = false;
      if(this.isCheckIn){
        this.$router.push('/');
      }
      // pulltoの場合、メインブロックの高さ
      this.$refs.articleListCol.style.height = window.innerHeight - 274 + 'px';
      this.isArticleLoading = false;
      this.currentGenreName = 'すべて';
      this.currentGenreId = 0;
      this.getGenreData();
      this.isTopMarkShow = false;
      this.$refs.clinicalListPull.scrollEl.addEventListener('scroll', this.isTopMarkCanShow);
    },
    getGenreData: function(){
      var _this = this;
      //TODO AJAX GET GENREDATA
      setTimeout(function () {
        _this.clinicalGenre = _this.clinicalGenre.concat(_this.fakeGenreData);
      }, 500);
    },
    goBack: function () {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    toggleArticleGenre: function (isShow) {
      // ジャンルモーダルの隠す
      this.isGenreVisible = isShow;
      if(isShow){
        this.$refs.clinicalListPull.scrollEl.style.overflow = 'hidden';
      } else {
        this.$refs.clinicalListPull.scrollEl.style.overflow = 'auto';
      }
    },
    changeGenre: function (genreId, genreName) {
      // ジャンル名を切り替え
      this.currentGenreName = genreName;
      this.currentGenreId = genreId;
      this.$refs.clinicalListPull.setBackScrollPos(0);
      this.$refs.articleListClinical.getArticleListByGenre(genreId);
    },
    refresh: function (loaded) {
      //pull down refresh
      if(this.isArticleLoading){
        loaded("fail");
        return;
      }
      this.$refs.articleListClinical.getArticleListByGenre(this.currentGenreId, loaded);
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.listScrollTop = this.$refs.clinicalListPull.getScrollPos();
    },
    goSearch: function () {
      // search pageに遷移する
      this.setListScrollTop();
      this.$router.push({ name: 'search', params: { tabId: this.tabId } });
    },
    scrollForLoad: function () {
      // pull up load article list
      if (this.isArticleLoading) {
        return;
      }
      this.isArticleLoading = true;
      let listHeight = this.$refs.articleListClinical.$refs.listBlock.clientHeight;
      let outerHeight = this.$refs.articleListCol.clientHeight;
      let loadingPos = listHeight - outerHeight;
      if (listHeight - outerHeight > 0 && this.$refs.clinicalListPull.getScrollPos() >= loadingPos - 48) {
        this.$refs.articleListClinical.getArticleList();
      } else {
        this.isArticleLoading = false;
      }
    },
    changeLoadingFlag: function(value){
      this.isArticleLoading = value;
    },
    isTopMarkCanShow: function(){
      this.isTopMarkShow = this.$refs.clinicalListPull.getScrollPos() >= 250;
    },
    goTop: function(){
      this.$refs.clinicalListPull.setBackScrollPos(0);
    }
  }
}
</script>

<style scope>
