  width: 24px;
  margin-top: -2px;
}

.clinicalQATopArea {
  position: relative;
  height: 177px;
  width: 100%;
  padding-top: 55px;
  overflow: hidden;
}

.clinicalQATopArea .topImgCol{
  position: absolute;
  height: 200px;
  width: 100%;
  top: -42px;
  background-repeat: no-repeat;
  background-size: cover;
}

.clinicalQA .clinicalQATop {
  width: 100%;
