  margin: 0 0 0 15px;
}

.clinicalQA .clinicalQATop .clinicalQATitleArea .introduceAreaDetail {
  width: 100%;
  height: 36px;
  font-size: 12px;
  color: var(--white-two);
  text-align: left;
