<template>
  <transition name="modal-fade">
    <div class="modal-backdrop" @click="close" v-show="show">
      <div class="modal" @click.stop role="dialog" aria-labelledby="modalTile" arial-describedby="modalDescription">
        <div :class="['modal-body', btnCount == 0 ? (isHaveImg ? 'pattern-0Btn-img' : 'pattern-0btn') : (btnCount == 1 ? 'pattern-1btn' : 'pattern-2btn')]">
          <div class="modal-image" v-if="isHaveImg">
            <img v-if="!isImgSvg" :src="pattern.modalImg.img" :srcset="pattern.modalImg.img2x+' 2x, ' + pattern.modalImg.img3x + ' 3x'"/>
            <img v-else :src="pattern.modalImg.img"/>
          </div>
          <p class="modal-body-Text" v-for="(text, index) in pattern.modalTexts" :key="index">{{text}}</p>
          <div v-show="hasReportTextarea" >
            <span style="float:right"><a id="reportSize">0</a>/200</span>
            <textarea id="reportTextarea" maxlength="200" class="reportTextarea" @keyup="countReportSize" placeholder="このコメントのどのような部分に対し、
不適切だと感じましたか？
200文字以内で詳細をご記載願います。" :value="textAreaValue"> </textarea>
          </div>
        </div>
        <div v-if="btnCount != 0" class="modal-bottom">
          <div :class="['bottom-btn', btnCount == 1 ? 'pattern-1btn' : 'pattern-2btn']" v-for="(btnName, index) in pattern.modalBtns" :key="index" @click="btnFunction(index)">{{btnName}}</div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import excMark from '@/assets/medyImages/exc-mark.png'
import excMark2x from '@/assets/medyImages/exc-mark@2x.png'
import excMark3x from '@/assets/medyImages/exc-mark@3x.png'
import clipImg from '@/assets/medyImages/navmenu-5-clippost.png'
import clipImg2x from '@/assets/medyImages/navmenu-5-clippost@2x.png'
import clipImg3x from '@/assets/medyImages/navmenu-5-clippost@3x.png'

export default {
  name: 'commonModal',
  props: {
    show: Boolean,
    patternName: ''
    },
  components: {
  },
  data () {
    return {
      pattern: Object,
      logOutPattern:{
        // logOutModal
        modalTexts:[
          'MEDYをログアウトしますか？',
          'ログアウトする場合は「はい」をクリック後、「medパス - ユーザ情報画面」よりログアウトをしてください。'
        ],
        modalBtns:[
          'いいえ',
          'はい'
        ],
      },
      testPattern:{
        // 1btnModalTest
        modalTexts:[
          '何かの操作が終わった時などに、確認する用途のダイヤローグ',
          '高さは内容に合わせ自由に可変可能。',
          '場所に合わせて変容してください。'
        ],
        modalBtns:[
          '確認'
        ],
      },
      test0Pattern:{
        // 1btnModalTest
        modalTexts:[
          '自動でフェードアウトするダイヤローグ',
          '高さ可変可能'
        ]
      },
      testImg0Pattern:{
        // 1btnModalTest
        modalTexts:[
          'ニュースフィードが設定されておりません',
        ],
        modalImg:{
          isImgSvg: false,
          img: excMark,
          img2x: excMark2x,
          img3x: excMark3x
        }
      },
      keyWordPattern:{
        modalTexts:[
          '検索キーワードを入力してください',
        ],
        modalBtns:[
          '閉じる'
        ],
      },
      reportPattern:{
        // reportModal
        modalTexts:[
          'ご報告いただいた内容は、MEDY運営事務局にて利用規約と照らし合わせ確認いたします。尚対応状況や結果についてのご連絡はいたしかねます。',
        ],
        modalBtns:[
          'キャンセル',
          '報告する'
        ],
      },
      reportedPattern:{
        // reportedPattern
        modalTexts:[
          '違反回答を受付ました。',
          'ご連絡いただいた回答が、ガイドラインに明らかに違反している場合はその回答をMEDYから削除し、回答者に対しての「ありがとう」没収などの措置を行います。',
          '※ご連絡にMEDYが個別にお答えすることはありません。',
          '※ご連絡に基づいてMEDYが対応。対策することをお約束するものではありません。',
          'ご連絡いただきありがとうございました。'
        ],
        modalBtns:[
          '確認'
        ],
      },
      sendCommentPattern:{
        modalTexts:[
          'コメントが投稿されました'
        ]
      },
      deleteCommentParrern:{
        modalTexts:[
          'コメントを削除した場合、',
          'このコメントに対して送られた「ありがとう」が削除されます'
        ],
        modalBtns:[
          'キャンセル',
          '削除'
        ],
      },

     sendMailPattern:{
        modalTexts:[
          '送信しました。'
        ],
        modalBtns:[
          '閉じる'
        ],
    },
      presenMsgParrern:{
        modalTexts:[
          'プレゼントに応募しました',
        ],
      },
      clipMsgParrern:{
        modalTexts:[
          'クリップポストに',
          '追加されました'
        ],
        modalImg:{
          isImgSvg: false,
          img: clipImg,
          img2x: clipImg2x,
          img3x: clipImg3x
        }

      },
      btnCount: 0,
      isHaveImg : false,
      isImgSvg: false,
      hasReportTextarea:false,
      reportSize:0,
      textAreaValue:""
    }
  },
  mounted: function(){
    this.init();
  },
  methods: {
    init: function(){
      if(this.patternName == 'logOut'){
        this.pattern = this.logOutPattern;
      } else if(this.patternName == 'test'){
        this.pattern = this.testPattern;
      } else if (this.patternName == 'noSelect') {
        this.pattern = this.testImg0Pattern
      } else if (this.patternName == 'keyWord') {
        this.pattern = this.keyWordPattern
      } else if (this.patternName == 'report') {
        document.getElementById("reportTextarea").value="";
        document.getElementById("reportSize").text = "0";
        this.pattern = this.reportPattern;
        this.hasReportTextarea = true
      } else if (this.patternName == 'reported') {
        this.pattern = this.reportedPattern;
      }else if (this.patternName == 'sendComment') {
        this.pattern = this.sendCommentPattern;
      }else if (this.patternName == 'deleteCommentDialog'){
        this.pattern = this.deleteCommentParrern;
      }else if (this.patternName == 'sendMail'){
        this.pattern = this.sendMailPattern
      }else if(this.patternName == 'presentMsg'){
        this.pattern = this.presenMsgParrern
      }else if(this.patternName == 'clipMsg'){
        this.pattern = this.clipMsgParrern
      }


      if(typeof(this.pattern.modalBtns) != "undefined" && this.pattern.modalBtns != null){
        this.btnCount = this.pattern.modalBtns.length;
      }
      if(typeof(this.pattern.modalImg) != "undefined" && this.pattern.modalImg != null){
        this.isHaveImg = true;
        if(typeof(this.pattern.modalImg.isImgSvg) != "undefined" && this.pattern.modalImg.isImgSvg != null){
          this.isImgSvg = this.pattern.modalImg.isImgSvg;
        }
      }
    },
    close: function () {
      if (this.patternName == 'report') {
        document.getElementById("reportSize").text = "0";
      }
      this.$emit('close');
    },
    btnFunction: function (index) {
      if(index == 0){
        this.$emit('btnFunction1');
      } else if(index == 1){
        this.$emit('btnFunction2');
      }
    },
    countReportSize: function (){
      document.getElementById("reportSize").text = document.getElementById("reportTextarea").value.length;
    }
  }
}
</script>

<style scoped>
.modal-fade-enter,
.modal-fade-leave-active {
    opacity: 0;
}
.modal-fade-enter-active,
.modal-fade-leave-active {
  transition: opacity .5s ease
}
.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--black-mask);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal {
  background-color: var(--white-two);
  box-shadow: 2px 2px 20px 1px;
  overflow-x:auto;
  display: flex;
  flex-direction: column; 
  border-radius: 16px;
  width: 290px;
  position: relative;
}

.modal .modal-body {
  position: relative;
  overflow: hidden;
}

.modal .modal-body .modal-image{
  width: 24px;
  height: 24px;
  display: inline-block;
  text-align: center;
  margin: 0 auto 12px 86px;
  overflow: hidden;
  vertical-align: top;
}

.modal .modal-body.pattern-0btn{
  padding: 29px 61px 29px 61px;
}

.modal .modal-body.pattern-0Btn-img{
  padding: 24px 45px 24px 46px;
}
.modal .modal-body.pattern-1btn{
  padding: 53px 16px 0 16px;
  margin-bottom: 47px;
  width: 258px;
}

.modal .modal-body.pattern-2btn {
  padding: 40px 20px 0 19px;
  margin-bottom: 38px;
  width: 251px;
}

.modal .modal-body .modal-body-Text{
  font-size: 14px;
  font-weight: 300;
  color: var(--black);
  line-height: 22px;
  margin-bottom: 18px;
}

.modal .modal-body .reportTextarea{
  font-family:'Noto Sans JP', sans-serif;
  min-height: 100px;
  width:100%;
  font-size: 14px;
  font-weight: 300;
  color: var(--black);
  line-height: 22px;
  margin-bottom: 0px;
  overflow-x:hidden;
}

.modal .modal-body .modal-body-Text:last-child{
  margin-bottom:0;
}

.modal .modal-body.pattern-0btn .modal-body-Text,
.modal .modal-body.pattern-0Btn-img .modal-body-Text{
  margin-bottom:0;
  text-align: center;
}

.modal .modal-bottom{
  bottom: 0;
  width: 100%;
  background: var(--tealish);
}

.modal .modal-bottom .bottom-btn{
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  color: var(--white-two);
}

.modal .modal-bottom .bottom-btn.pattern-1btn{
  width: 100%;
  height: 44px;
  line-height: 42px;
  box-shadow: inset 0 1px 0 0 var(--white-three);
}

.modal .modal-bottom .bottom-btn.pattern-2btn{
  width: 50%;
  height: 42px;
  line-height: 40px;
  box-shadow: inset 0 1px 0 0 var(--white-three), inset -1px 0 0 0 var(--brown-grey);
}

.modal .modal-bottom .bottom-btn.pattern-2btn:last-child{
  box-shadow: inset 0 1px 0 0 var(--white-three);
}


</style>