<template>
  <div class="pickupArticle" @click="gotoArticleDetail(article.articleId)">
    <div class="image" :style="makeBackImgUrl"></div>
    <div class="titleIndex">
      <span>{{sortNo}}</span>
    </div>
    <div class="title">
      <span class="titletest">{{article.articleTitle}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "pickUpArticle",
  props: {
    article: Object,
    sortNo: Number
  },
  data() {
    return {
    };
  },
  created: function () {
  },
  mounted: function () {
  },
  activated: function () {
  },
  computed: {
    makeBackImgUrl: function () {
      return 'background-image:url(' + this.article.articleImg + ')';
    }
  },
  methods: {
    gotoArticleDetail: function (id) {
      this.$emit('setListScrollTop');
      this.$router.push({ name: 'articleDetail', params: { articleId: id } });
    }
  }
};
</script>

<style scope>
.pickupArticle {
  width: 100%;
  height: 70px;
  box-shadow: inset 0 -1px 0 0 var(--white-three);
  position: relative;
}

.pickupArticle .image {
  width: 74px;
  height: 46px;
  margin-top: 12px;
  margin-left: 12px;
  background-color: var(--black-10);
  background-repeat: no-repeat;
  background-attachment: inherit;
  background-size: cover;
  display: inline-block;
}

.pickupArticle .titleIndex {
  position: absolute;
  top: 28px;
  margin-left: 94px;
  width: 8px;
  height: 15px;
  font-family: Roboto;
  font-size: 13px;
  font-weight: 500;
  line-height: 15px;
  color: var(--tealish);
}

.pickupArticle .title {
  position: absolute;
  top: 15px;
  height: 40px;
  margin-left: 107px;
  width: calc((100% - 119px));
  height: 40px;
  display: flex;
  align-items: left;
  justify-content: center;
  flex-direction: column;
}

.pickupArticle .title span {
  max-height: 40px;
  word-wrap: break-word;
  word-break: break-all;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  font-size: 14px;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 300;
  vertical-align: middle;
  display: -webkit-box;
  color: var(--black);
}
</style>