<template>
<div>
    <div class="ticketHead" >
      <div class="medyTicketTitle">
        <div class="backArrow" @click="goBack()" v-show="shouldShowBackButton">
          <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" aria-hidden="true" width="0.57em" height="1em" style="-ms-transform: rotate(360deg); -webkit-transform: rotate(360deg); transform: rotate(360deg);" preserveAspectRatio="xMidYMid meet" viewBox="0 0 576 1024"><path d="M528 0q14 0 24 10 4 3 6 7t3 8.5 1 9-1 8.5-3 8-6 8L96 515l450 450q6 6 8.5 15t0 18-8.5 15q-10 10-24.5 10t-24.5-10L23 539q-10-10-10-24t10-24L504 10q10-10 24-10z" fill="white"/></svg>
        </div>

        <div class="medyTicketLabel">
          <span>MEDYチケット</span>
        </div>
      </div>
    </div>

    <PullTo :style="pageDivCss"
      :is-top-bounce="false"
      :is-bottom-bounce="false"
      :top-load-method="refresh"
      @scroll="loadMore()"
      :topConfig="pageHeaderConfig">

      <div id="ticketBody" >
        <div class="ticketRecTop" id="ticketRecTop">
          <div class="ticketRecText" >
            所有MEDYチケット
          </div>

        <div class="ticketInfo">
          <div class="ticketCount">
            99
          </div>

          <div class="ticketCountLabel">
            チケット
          </div>
        </div>
        
        <div class="line"></div>

        <div class="notice">
          <div class="noticeBtn" @click="showNotice">
            <span> MEDYチケットについて </span>
            <img src="@/assets/common/up.png" v-show="shouldShowNotice" >
            <img src="@/assets/common/down.png" v-show="!shouldShowNotice" >
          </div>
          <div class="noticeText" v-show="shouldShowNotice">
            <span> 「MEDYチケット」は、MEDY内のコンテンツを利用することで貯めることができるポイントです。自身のコメントに他のユーザーから「ありがとう」が送られたり、他のユーザーのコメントへ「ありがとう」を送ることで貯まっていきます。 </span>
          </div>
        </div>
      </div>

      <div class="ticketTab" id="ticketTab">
        <div :class="getPreCss()" @click="selPre">
          <span>プレゼント一覧</span>
        </div>
        <div :class="getUseRecCss()" @click="selUseRec">
          <span>チケット利用履歴</span>
        </div>
      </div>

      <div class="ticketInfo" id="ticketInfo">
        <div class="presentList" v-if="shouldSelPre">
          <Present v-for="(present, index) in preSentList" :key="index" :present="present"/>
        </div>
        <div class="useRecList" v-if="!shouldSelPre && !testNa">
          <TicketUseRecord v-for="(useRecord, index) in userRecList" :key="index" :useRecord="useRecord"/>
        </div>
        <div class="noRecord" v-if="!shouldSelPre && testNa">
          履歴がありません。
        </div>
      </div>

      <diV id="loadingArea" class="loading" v-if="shouldSelPre || !testNa" >
          <img src="@/assets/common/reload.svg"> <span class="reread">再読み込み</span>
      </diV>
  </div>
  </PullTo>
</div>
</template>

<script>
import Present from "@/components/cmptsCommon/Present.vue";
import TicketUseRecord from "@/components/cmptsCommon/TicketUseRecord.vue";
import PullTo from "@/components/vue-pull-to/article-pull-to.vue";

export default {
  name: 'articleDetail',
  data() {
    return {
      pageDivCss: "height:100%;margin-top:42px;overflow-y: hidden;",
      testNa:true,
      shouldShowBackButton:false,
      shouldShowNotice:false,
      shouldSelPre:true,
      isloading:false,
      pageHeaderConfig: {
        pullText: "リフレッシュ",
        triggerText: "リフレッシュ",
        loadingText: "リフレッシュ中",
        doneText: "",
        failText: "",
        loadedStayTime: 400,
        stayDistance: 50,
        triggerDistance: 70
      },
      preSentList:[
        {
          presentName : "ハーゲンダッツミニカップ",
          presentImg:"http://coffeedoctors.jp/doctors/wp-content/uploads/sites/2/2018/08/main.jpg",
          presentPrice:10
        },
        {
          presentName : "ハーゲンダッツミニカップテストテストテストテストテストテストテストテストテストテスト",
          presentImg:"http://coffeedoctors.jp/doctors/wp-content/uploads/sites/2/2018/08/main.jpg",
          presentPrice:10
        },
        {
          presentName : "ハーゲンダッツミニカップ",
          presentImg:"http://coffeedoctors.jp/doctors/wp-content/uploads/sites/2/2018/08/main.jpg",
          presentPrice:10
        },
      ],
      userRecList:[
        {
          preTime : "yyyy年mm月dd日",
          preName : "ハーゲンダッツミニカップ",
          preCount:20,
          preTicket:"- 200チケット",
        },
        {
          preTime : "yyyy年mm月dd日",
          preName : "ハーゲンダッツミニカップテストテストテストテストテストテストテストテストテストテスト",
          preCount:20,
          preTicket:"- 200チケット",
        },{
          preTime : "yyyy年mm月dd日",
          preName : "ハーゲンダッツミニカップ",
          preCount:20,
          preTicket:"- 200チケット",
        },{
          preTime : "yyyy年mm月dd日",
          preName : "ハーゲンダッツミニカップ",
          preCount:20,
          preTicket:"- 200チケット",
        },{
          preTime : "yyyy年mm月dd日",
          preName : "ハーゲンダッツミニカップ",
          preCount:20,
          preTicket:"- 200チケット",
        },{
          preTime : "yyyy年mm月dd日",
          preName : "ハーゲンダッツミニカップ",
          preCount:20,
          preTicket:"- 200チケット",
        },
      ]
    }
  },
  components: {
    Present,
    TicketUseRecord,
    PullTo
  },
  computed: {
  },
  mounted: function() {
    this.pageDivCss =
      "height:" +
      (window.innerHeight - 42 ) +
      "px;margin-top:42px;margin-bottom:0px;overflow:hidden;";
  },
  activated: function() {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
    }
    this.shouldShowBackButton = true
  },
  methods: {
    refresh: function(loaded) {
      loaded("done");
      return;
    },
    goBack: function() {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    close: function() {
      if (this.$store.getters.canGoback) {
        this.$router.go(this.$store.getters.gobackCount)
      } else {
        this.$router.push('/')
      }
    },
    showNotice: function(){
      this.shouldShowNotice = !this.shouldShowNotice;
    },
    getPreCss: function(){
      var className = "presentList" ;
      if(this.shouldSelPre){
        className += " active";
      }
      return className;
    },
    getUseRecCss: function(){
      var className = "ticketUserRecord" ;
      if(!this.shouldSelPre){
        className += " active";
      }
      return className;
    },
    selPre: function(){
      this.shouldSelPre = true;
      this.isloading = false;
    },
    selUseRec: function(){
      this.shouldSelPre = false;
      this.isloading = false;
    },
    addPresentList: function(){
      for(var i = 0 ; i < 3;i++){
        this.preSentList.push(
          {
            presentName : "ハーゲンダッツミニカップ",
            presentImg:"http://coffeedoctors.jp/doctors/wp-content/uploads/sites/2/2018/08/main.jpg",
            presentPrice:10
          }
        )
      }
      this.isloading = false;
    },
    addUseRecordList: function(){
      for(var i = 0 ; i < 3;i++){
        this.userRecList.push(
          {
            preTime : "yyyy年mm月dd日",
            preName : "ハーゲンダッツミニカップ",
            preCount:20,
            preTicket:"- 200チケット",
          }
        )
      }
      this.isloading = false;
    },
    loadMore: function() {
      // var _this = this;
      // var allHeight = document.getElementById("ticketBody").scrollHeight;
      // var needRefreshHeight = allHeight - window.innerHeight + 42 - 28;
      // var scrollTop = document.getElementById("ticketBody").scrollTop;
      // if (scrollTop >= needRefreshHeight && !_this.isloading) {
      //   _this.isloading = true;
      //   setTimeout(function() {
      //     if(_this.shouldSelPre){
      //       _this.addPresentList();
      //     }else{
      //       _this.addUseRecordList();
      //     }
      //   }, 800);

      // }
      return;
    },


  }
}
</script>

