    top:0px;
    width: 100%;
  }

  .medyTicketTitle{
		width: 100%;
		height: 42px;
		box-shadow: inset 0 -1px 0 0 #e7e7e7;
		background-color: #303747;
	}

	.medyTicketTitle .backArrow{
    margin-top:14px;
    margin-left:12px;
		width: 24px;
    height: 24px;
    display: inline-block;
	}

  .medyTicketTitle .medyTicketLabel{
    left: calc((100% - 106px ) / 2);
    top: 12px;
    width: 106px;
    height: 24px;
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
    display: inline-block;
    position: absolute;
  }

