    height:36px;
  }

  .ticketRecTop .notice .noticeBtn img{
    float:right;
    padding-right:12px;
    width: 24px;
    height: 24px;
    padding-top:6px;
  }

  .ticketRecTop .notice .noticeText{
    font-size: 12px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    background-color:var(--white-four);
    color: #999999;
    padding-left: 12px;
    padding-top: 12px;
    padding-right: 12px;
  }

  .ticketTab{
    width: 100%;
    height: 48px;
  }

  .ticketTab .presentList{
    width: 50%;
    height: 48px;
    display: inline-block;
    text-align: center;
    box-shadow: inset 0 -1px 0 0 var(--white-four), inset -1px 0 0 0 var(--white-four);
    background-color: var(--white-four);
    font-size: 14px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: 48px;
    letter-spacing: normal;
    text-align: center;
    color: var(--black);
  }

  .ticketTab .ticketUserRecord{
