<template>
  <div class="maskDiv" :style="mask()"> </div>
</template>

<script>

export default {
  name: "MaskDiv",
  props: {
      target:{
        type:Element,
        default: null
      },
      maskWidth: {
        type: Number,
        default: 0
      },
      maskHeight: {
        type: Number,
        default: 0
      },
      maskLeft: {
        type: Number,
        default: 0
      },
      maskTop: {
        type: Number,
        default: 0
      },
      isShow: {
        type: Boolean,
        default: false
      },
  },
  components: {
  },
  data() {
    return {
       
    };
  },
  methods:{
    mask:function(){
        var style = "background-color:rgba(0, 0, 0, 0.1);";
        style = style + "width:" + this.maskWidth + "px;height: " + this.maskHeight +"px;";

        style = style + "position:absolute;left:" + this.maskLeft + "px;top: " + this.maskTop +"px;";

        if(this.isShow){
            return style + "display:block;z-index:100";
        }
        return style + "display:none;";
    }
  },
  created: function () {
  },
  mounted: function () {
  },
  activated: function () {
  }
};
</script>

<style scope>
</style>