		},
		components: {
			PullTo
		},
		mounted: function() {
			this.pageHeight = 'height:' + window.innerHeight + 'px';
		},
		activated: function() {},
		methods: {
			toKeywordSet: function() {
				this.$router.push({
					name: 'Keywordset'
				})
			},



			goBack: function() {
				if (window.history.length > 1) {
					this.$router.go(-1)
				} else {
					this.$router.push('/')
				}


			},




			intEditor() {
				//        alert(this.$store.state.overSeasData[1].name)
				this.$router.push({
					name: 'interestType'
				})
			},
			oveEditor() {
				//        alert(this.$store.state.interestData[1].checked)
				this.$router.push({
					name: 'overseas'
				})
			}

		}
	}
</script>

