<template>
  <div class="news-top">
    <div class="newArticleMask">
      <span class="newAreaTitle">新着</span>
    </div>
    <div class="topArea" ref="scrollXArea">
      <div class="imageArticle" ref="newArticlesWidCol">
        <new-article :isFirst="(index == 0)" :article="item" v-for="(item, index) in articles" :key="index" @setListScrollTop="setListScrollTop"/>
      </div>
    </div>
  </div>
</template>

<script>
import ArticleForNew from "@/components/cmptsCommon/ArticleForNew.vue"

export default {
  name: 'newsArticles',
  props: {
    menuId: Number
  },
  data() {
    return {
      articles: [],
      newsScrollLeft: 0,
      fakeArticles: [
        {
          articleId: "310001",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleSiteName: "サイト名",
          articleStartDate: "yyyy年mm月dd日"
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleSiteName: "サイト名",
          articleStartDate: "yyyy年mm月dd日"
        },
        {
          articleId: "310003",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleSiteName: "サイト名",
          articleStartDate: "yyyy年mm月dd日"
        }
      ]
    };
  },
  components: {
    "new-article": ArticleForNew
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      this.$refs.scrollXArea.scrollLeft = this.newsScrollLeft;
      this.newsScrollLeft = 0;
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      this.newsScrollLeft = 0;
      this.articles = [];
      this.$refs.scrollXArea.scrollLeft = 0;
      this.$refs.scrollXArea.addEventListener('scroll', this.listenScrollX, false);
      this.$refs.scrollXArea.addEventListener('touchend', this.reSetScroll, false);
      this.$refs.scrollXArea.addEventListener('touchcancel', this.reSetScroll, false);
      // ニュース記事ブロックの幅を取得
      this.getNewsArticles();
    },
    listenScrollX: function () {
      if (this.$store.state.isForbiddenPullDown) {
        return;
      }
      this.$store.commit('makeScrollForbiddenPullDown');
    },
    reSetScroll: function () {
      if (!this.$store.state.isForbiddenPullDown) {
        return;
      }
      this.$store.commit('makeScrollCanPullDown');
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.$emit('setListScrollTop');
    },
    setScrollX: function () {
      this.newsScrollLeft = this.$refs.scrollXArea.scrollLeft;
    },
    getNewsArticles: function () {
      var _this = this;
      //TODO AJAX
      setTimeout(function () {
        _this.articles = _this.fakeArticles;
        if(typeof(_this.$refs.newArticlesWidCol) != "undefined" && _this.$refs.newArticlesWidCol != null){
          _this.$refs.newArticlesWidCol.style.width = _this.articles.length * 320 + (_this.articles.length - 1) * 16 + 24 + 'px';
        }
      }, 1000);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.news-top {
  margin-left: 0px;
  height: 308px;
  width: 100%;
  overflow: hidden;
  background-color: var(--white);
}

.news-top .topArea {
  width: auto;
  height: 325px;
  background-color: var(--white);
  overflow-y: hidden;
  margin-top: 8px;
}

.news-top .newArticleMask {
  height: 36px;
  margin-top: 20px;
  padding-left: 12px;
}

.news-top .newArticleMask .newAreaTitle{
  font-size: 18px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 36px;
  letter-spacing: normal;
  color: var(--black);
}

.imageArticle {
  width: 700px;
  height: 244px;
  overflow-y: hidden;
}
</style>
