				<span class="fontStyle" v-if="!hasCliped">記事をクリップする</span>
                <span class="fontStyle" v-if="hasCliped">記事のクリップを外す</span>
			</button>
			<div class="copyLink" >
                リンクをコピー
                <div class="shareLinks"> 
                    <img src="@/assets/common/facebook.jpg" >
                    <img src="@/assets/common/twitter.jpg" >
                    <img src="@/assets/common/line.png" >
                </div> 
            </div>
		</div>

        <div class="articleTop">
			<div class="chronicleTime">
