<template>
  <div class="userActive">
    <div class="navigation">
      <img class="navigation_back" src="../assets/common/back.png" @click="goBack()" v-if="shouldShowBackButton">
      <img class="navigation_close" src="../assets/common/close.png" @click="close()">
    </div>
    <p class="title">This is user active</p>
    <br>
    <div class="routeButton" @click="gotoArticleDetail()">記事詳細へ</div>
  </div>
</template>

<script>
export default {
  name: 'userActive',
  data() {
    return {
      shouldShowBackButton: false
    }
  },
  components: {
  },
  mounted: function() {
  },
  activated: function() {
    if (this.Global.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
    }
    this.shouldShowBackButton = this.$store.getters.shouldShowGoBack
  },
  computed: {
  },
  methods: {
    gotoArticleDetail: function() {
      this.$router.push({ name: 'articleDetail', params: { articleId: 100163} })
    },
    goBack: function() {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    close: function() {
      if (this.$store.getters.canGoback) {
        this.$router.go(this.$store.getters.gobackCount)
      } else {
        this.$router.push('/')
      }
    }
  }
}
</script>

<style>
  .userActive .navigation {
    background-color: #CDCDCD;
    width: 100%;
    height: 44px;
    border-top: 8px solid #20BCC4;
    border-bottom: 3px solid #EEAD0E;
  }
  .userActive .title {
    width: 100%;
    font-size: 16px;
    color: #7D7D7D;
    font-weight: 700;
    text-align: center;
  }
  .userActive .routeButton {
    height: 30px;
    margin: 0px 0px 10px 0px;
    border: 2px solid #7D7D7D;
    text-align: center;
    font-size: 16px;
    color: #7D7D7D;
    word-wrap: break-word;
    font-weight: 700;
    line-height: 30px;
    cursor: pointer;
  }
  .userActive .navigation .navigation_back {
    width: 20px;
    margin: 16px auto 10px 15px;
    float: left;
    cursor: pointer;
  }
  .userActive .navigation .navigation_close {
    width: 30px;
    margin: 10px 15px 10px auto;
    float: right;
    cursor: pointer;
  }
</style>
