import Vue from 'vue'
import Router from 'vue-router'

import Top from './views/Top.vue'
import TopDemo from './views/TopDemo.vue'
import ClinicalList from './views/ClinicalList.vue'
import ArticleDetail from './views/ArticleDetail.vue'
import ArticleDetailTest from './views/ArticleDetailTestNa.vue'
import UserActive from './views/UserActive.vue'
import Search from './views/Search.vue'
import InterestType from './views/InterestType.vue'
import MedyTicket from './views/MedyTicket.vue'
import MedyTicketTest from './views/MedyTicketTestNa.vue'
import Overseas from './views/Overseas.vue'
import Notice from './views/Notice.vue'
import NoticeList from './views/NoticeList.vue'
import KeyWordSet from './views/KeyWordSet.vue'
import MailSet from './views/MailSet.vue'
import Inquiry from './views/Inquiry.vue'
import MyMedicalNews from './views/MyMedicalNews.vue'
import MyMedicalNewsError1 from './views/MyMedicalNewsError1.vue'
import MyMedicalNewsError2 from './views/MyMedicalNewsError2.vue'
import MyMedicalNewsError3 from './views/MyMedicalNewsError3.vue'
import MyMedicalNewsError4 from './views/MyMedicalNewsError4.vue'
import MyMedicalNewsSetting from './views/MyMedicalNewsSetting.vue'
import GiftApplication from './views/GiftApplication.vue'
import MedicalOpinions from './views/MedicalOpinions.vue'
import TimeLine from './views/TimeLine.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'top',
      component: Top,
      meta: {
        index: 1,
      }
    },
    {
      path: '/clinicalList',
      name: 'clinicalList',
      component: ClinicalList,
      meta: {
        index: 1
      }
    },
    {
      path: '/article/:articleId',
      name: 'articleDetail',
      component: ArticleDetail,
      meta: {
        index: 2
      }
    },
    {
      path: '/articleTest/:articleId',
      name: 'articleDetailTest',
      component: ArticleDetailTest,
      meta: {
        index: 2
      }
    },
    {
      path: '/userActive/:userId',
      name: 'userActive',
      component: UserActive,
      meta: {
        index: 2
      }
    },
    {
      path: '/test',
      name: 'TopDemo',
      component: TopDemo,
      meta: {
        index: 1
      }
    },
    {
      path: '/search/:tabId',
      name: 'search',
      component: Search,
      meta: {
        index: 1,
      }
    },
    {
      path: '/search/:tab',
      name: 'search',
      component: Search,
    },
    {
      path: '/interest',
      name: 'interestType',
      component: InterestType,
    },
    {
      path: '/medyTicket',
      name: 'MedyTicket',
      component: MedyTicket,
      meta: {
        index: 1
      }
    },
    {
      path: '/MedyTicketTest',
      name: 'MedyTicketTest',
      component: MedyTicketTest,
      meta: {
        index: 1
      }
    },
    
    {
      path: '/overseas',
      name: 'overseas',
      component: Overseas,
    },

    {
      path: '/notice',
      name: 'notice',
      component: Notice,
      meta: {
        index: 1
      }
    },
    {
      path: '/noticelist',
      name: 'noticelist',
      component: NoticeList,
      meta: {
        index: 2
      }
    },
    {
      path: '/keywordset',
      name: 'Keywordset',
      component: KeyWordSet,
    },
    {
      path: '/mailset',
      name: 'mailset',
      component: MailSet,
    },

    {
      path: '/inquiry',
      name: 'Inquiry',
      component: Inquiry,
      meta: {
        index: 2
      }
    },
    {
      path: '/medicalNews',
      name: 'medicalNews',
      component: MyMedicalNews,
      meta: {
        index: 1
      },
				
    },
		
		
		
    {
      path: '/medicalNewsError1',
      name: 'medicalNewsError1',
      component: MyMedicalNewsError1,
      meta: {
        index: 1
      },		
    },
				
    {
      path: '/medicalNewsError2',
      name: 'medicalNewsError2',
      component: MyMedicalNewsError2,
      meta: {
        index: 1
      },		
    },
				
				
    {
      path: '/medicalNewsError3',
      name: 'medicalNewsError3',
      component: MyMedicalNewsError3,
      meta: {
        index: 1
      },		
    },				
				
    {
      path: '/medicalNewsError4',
      name: 'medicalNewsError4',
      component: MyMedicalNewsError4,
      meta: {
        index: 1
      },		
    },				
				
		
    {
      path: '/medicalNewsSetting',
      name: 'medicalNewsSetting',
      component: MyMedicalNewsSetting,
      meta: {
        index: 1
      }
    },
    {
      path: '/medicalOpinions',
      name: 'medicalOpinions',
      component: MedicalOpinions,
      meta: {
        index: 1
      }
    },
    {
      path:'/giftApplication',
      name:'giftApplication',
      component: GiftApplication,
			meta: {
			  index: 2
			}
    },
    {
      path: '/timeline',
      name: 'timeline',
      component: TimeLine,
      meta: {
        index: 1,
      }
    }

  ]
})
