<template>
  <transition name="modal-fade">
    <div class="modal-backdrop" @click="close" v-show="show">
      <div class="modal" @click.stop role="dialog" aria-labelledby="modalTile" arial-describedby="modalDescription">
        <div class="modal-header" id="modalTitle">
            <div style="position:relative">
              <span class="thanksLabel">
                  100ありがとう
              </span>
              <span @click="close" aria-label="Close modal" style="float:right;"> 
                <img class="btn-close" src="@/assets/common/close-button.png" >
              </span>
            </div>
        </div>
        
        <div class="modal-body" id="modalDescription">
          <ThanksPullTo :is-top-bounce="true" :is-bottom-bounce="false" :top-load-method="refresh" 
            :topBlockHeight="40"  @scroll="handleScroll" :topConfig="pageHeaderConfig"  >
            <div>
              <ThanksUserInfo :key="index" v-for="(thanksInfo, index) in ThanskInfoList" :thanksInfo="thanksInfo"/> 
            </div>
          </ThanksPullTo>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import ThanksUserInfo from '@/components/cmptsCommon/ThanksUserInfo.vue';
import ThanksPullTo from '@/components/vue-pull-to/thanksPopup-pull-to.vue'

export default {
  name: 'Modal',
  props: ['show'],
    components: {
      ThanksUserInfo,
      ThanksPullTo
    },
  data () {
    return {
      isloading:false,
      pageHeaderConfig: {
            pullText: 'リフレッシュ',
            triggerText: 'リフレッシュ',
            loadingText: 'リフレッシュ中',
            doneText: '',
            failText: '',
            loadedStayTime: 200,
            stayDistance: 50,
            triggerDistance: 30
      },
      ThanskInfoList:[
        {
          image: "http://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "9文字制限なのかな",
          siteInfo: "臨床Q&A記事一覧",
          department: "泉ガーデンクリニック",
          age: "40代",
          followed:true
        },
        {
          image: "http://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "9文字制限なのかな",
          siteInfo: "臨床Q&A記事一覧",
          department: "泉ガーデンクリニック",
          age: "30代",
          followed:false
        },
        {
          image: "http://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "9文字制限なのかな",
          siteInfo: "臨床Q&A記事一覧",
          department: "泉ガーデンクリニック",
          age: "30代",
          followed:false
        },
        {
          image: "http://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "9文字制限なのかな",
          siteInfo: "臨床Q&A記事一覧",
          department: "泉ガーデンクリニック",
          age: "30代",
          followed:false
        },
        {
          image: "http://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "9文字制限なのかな",
          siteInfo: "臨床Q&A記事一覧",
          department: "泉ガーデンクリニック",
          age: "30代",
          followed:false
        },
        {
          image: "http://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "9文字制限なのかな",
          siteInfo: "臨床Q&A記事一覧",
          department: "泉ガーデンクリニック",
          age: "30代",
          followed:false
        },
        {
          image: "http://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "9文字制限なのかな",
          siteInfo: "臨床Q&A記事一覧",
          department: "泉ガーデンクリニック",
          age: "30代",
          followed:false
        }
        
      ]
    }
  },
  methods: {
    close: function () {
      this.$emit('close');
    },
    handleScroll: function () {
        var _this = this;
        var needRefreshHeight = document.getElementById("thanksScroll").scrollHeight - 400;
        var scrollTop = document.getElementById("thanksScroll").scrollTop;
        if(scrollTop >= needRefreshHeight && !_this.isloading){
            _this.isloading = true;
            setTimeout(function(){_this.addOpinion()},800);
        }
    },
    refresh: function (loaded) {
      loaded('done');
      return;
    },
    addOpinion: function(){
      for(var i=0;i<5;i++){
          this.ThanskInfoList.push(
            {
              image: "http://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
              userName: "9文字制限なのかな",
              siteInfo: "臨床Q&A記事一覧",
              opinion: "13",
              age: "30代",
              followed:false
            }
          )
      }
      this.isloading = false;
    },
  }
}
</script>

