<template>
  <div class="articleArea" @click="gotoArticleDetail(article.articleId)">
    <div class="imageArea">
      <img class="articleImgage" :src="article.articleImg">
    </div>
    <div class="infoArea">
      <div class="siteName">{{article.articleTypeName}}</div>
      <div class="title">{{article.articleTitle}}</div>
      <div class="opinionArea">
        <span class="opinion" v-if="article.articleType == 1 && article.commentDisplayFlag != 0">{{article.commentCount}} Opinions</span>
        <span class="opinion" v-if="article.articleType == 1 && article.commentDisplayFlag == 0">閲覧のみ</span>
        <span class="nMinute">{{article.articleStartDate}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "articleCommon",
  props: {
    article: Object
  },
  data() {
    return {
    };
  },
  created: function () {
  },
  mounted: function () {
  },
  activated: function () {
  },
  methods: {
    gotoArticleDetail: function (id) {
      this.$emit('setListScrollTop');
      if(this.article.articleType == 0){
        this.$router.push({ name: 'articleDetail', params: { articleId: id } });
      } else {
        //TODO AD DETAIL
        this.$router.push({ name: 'articleDetail', params: { articleId: id } });
      }
      
    }
  }
};
</script>

