<template>
<div>
    <!-- <CommonModal :show="reportVisabled" :patternName="modalReport" @close="reportClose" @btnFunction1="reportClose" @btnFunction2="opinionReport"/>
    <CommonModal :show="reportedVisabled" :patternName="modalReported" @close="reportedClose" @btnFunction1="reportedClose"/>
    <CommonModal :show="shouldshowDeleteDialog" :patternName="modalDelDialog" @close="deleteCancel" @btnFunction1="deleteCancel" @btnFunction2="deleteMycomment"/> -->
    <div class="myComment" v-if="hasMycomment()">
        <div class="myCommentTitle">
            <span class="fontStyle"> 自分のコメント </span>
        </div>
        <div class="head">
            <span class="time"> {{myComment==null ? "" : myComment.nMinute}} </span> 
            <span class="menuButton" @click="myCommentMenuClick()">
                <img src="@/assets/common/group-6.svg">
            </span>
        </div>
        <div :class="controllMenu(myComment==null ? false :myComment.showMenuButton)">
            <div class="edit" @click="editComment"> 
                <span >
                    編集する 
                </span>
            </div> 
            <div class="delete" @click="showDeleteDialog">
                <span >
                    削除
                </span>
            </div>
        </div>
        <div class="commentDetail">
            <span>
                {{myComment==null ? "" :myComment.commentDetail}}
            </span>
        </div>
        <div class="thanksDetailArea">
            <div class="thanksNumbers" @click="showModal()" >
                <a style="text-decoration: none;">
                    <span class="text_1">{{myComment==null ? 0 :myComment.thanksCount}}&nbsp;</span>
                </a> 
                <span class="text_2">ありがとう</span>
            </div>
        </div>
    </div>

	<div class="option" id="option_top">
        <div class="title">
            <span class="font largeFont"> Opinions </span> <span class="number">
                {{commentCount}}件 </span>
        </div>
        <div class="order">
            <span @click="orderTipDefaultSelected = true" :class="getSelectedCss(orderTipDefaultSelected) + ' tip_1'">デフォルト順</span> 
            <span @click="orderTipDefaultSelected = false" :class="getSelectedCss(!orderTipDefaultSelected) + ' tip_2'">|&nbsp;&nbsp;&nbsp;投稿順</span>
        </div>

        <div class="situation" v-for="(opinionInfo, index) in opinions" :key="index" v-show="orderTipDefaultSelected">
            <div class="pickUp" v-if="opinionInfo.isPickup">
                <span class="fontStyle"> Pick Up </span>
            </div>
            <div :class="getOpinionHead(opinionInfo.isPickup)">
                <span class="time"> {{opinionInfo.nMinute}} </span> 
                <span class="menuButton" @click="opinionMenuClick(index)" >
                    <img src="@/assets/common/group-6.svg">
                </span>
            </div>
            <div :class="controllMenu(opinionInfo.showMenuButton)">
                <div class="report" v-if="!opinionInfo.reported" @click="showReportModal(index)"> 
                    <span>
                        不適切なコメントの報告 
                    </span>
                </div> 
                <div class="reported" v-if="opinionInfo.reported"> 
                    <span >
                        コメント違反報告済み
                    </span>
                </div> 
            </div>
            <div class="commentDetail">
                <span>
                    {{opinionInfo.commentDetail}}
                </span>
            </div>
            <div class="thanksDetailArea">
                <button :class="getThanksCss(opinionInfo.hasThanked)" id="thankBtn" @click="thanks(index)">
                    <span class="content"> ありがとう </span>
                </button>
                <div class="thanksNumbers" @click="showModal()" >
                    <a style="text-decoration: none;">
                        <span class="text_1">{{opinionInfo.thanksCount}}&nbsp;</span>
                    </a> 
                    <span class="text_2">ありがとう</span>
                </div>
            </div>
            <div class="userAttribute">
                <div class="mask">
                    <img :src="opinionInfo.imageAvatar">
                </div>
                <div class="information">
                    <p class="message_1">
                        {{opinionInfo.userName}} <img src="@/assets/medyImages/official.png" v-if="opinionInfo.userAuth">
                    </p>
                    <p class="message_2">{{opinionInfo.userDepartment}}</p>
                    <p class="message_3">{{opinionInfo.userJob}} {{opinionInfo.userAge}}</p>
                </div>
                <button class="follow" v-if="!opinionInfo.isFollowed" @click="changeFollow(index)" >
                    フォローする
                </button>
                <button class="followed" v-if="opinionInfo.isFollowed" @click="changeFollow(index)" >
                    フォロー中
                </button>
            </div>
        </div>
        
        <diV id="loadingArea" class="reread_area" v-show="orderTipDefaultSelected">
            <!-- <img src="@/assets/common/reload.svg"> <span class="reread">再読み込み</span> -->
            <span class="reread">loading…</span>
        </diV>

        <diV class="noCommentDiv" v-show="!orderTipDefaultSelected">
            コメントがありません。
        </diV>

        
	</div>
