<script>
// import ThanksModal from '@/components/cmptsCommon/ThanksModal.vue'
// import CommonModal from "@/components/cmptsModal/CommonModal.vue"

export default {
  name: "QADetailTop",
  props: {
      opinions:Array,
      myComment:Object,
    //   isModalVisible: {
    //     type: Boolean,
    //     default: false
    //   }
  },
  components: {
    // ThanksModal,
    // CommonModal
  },
  data() {
    return {
      commentCount:46,
      orderTipDefaultSelected:true,
    //   reportIndex:null,
    //   reportVisabled:false,
    //   reportedVisabled:false,
    //   modalReport: 'report',
    //   modalReported: 'reported',
    //   shouldshowDeleteDialog:false,
    //   modalDelDialog: 'deleteCommentDialog'
    };
  },
  methods:{
    getOpinionHead : function (isPickup) {
        if(isPickup){
            return "pickUpHead"
        }
        return "head";
    },
    hasMycomment:function(){
        if(this.myComment != undefined && this.myComment != null){
            return true;
        }
        return false
    },
    showModal: function () {
        this.$emit('showModal');
    },
    closeModal: function () {
        this.$emit('closeModal');
    },
    changeFollow: function (index) {
        this.opinions[index].isFollowed = !this.opinions[index].isFollowed
    },
    thanks: function (index) {
        this.opinions[index].hasThanked = !this.opinions[index].hasThanked
    },
    getThanksCss: function(hasThanked){
        if(hasThanked){
            return "btnThanked";
        }
        return "btnThanks";
    },
    getSelectedCss: function(selected){
        if(selected){
            return "selected";
        }
        return "";
    },
    controllMenu: function(showMenuButton){
        if(showMenuButton){
            return "controll "
        }else{
            return "controll hidden"
        }
    },
    myCommentMenuClick: function(){
        this.$emit("myCommentMenuClick");
    },
    opinionMenuClick: function(index){
        
        this.$emit("opinionMenuClick", index);
    },
    opinionReport: function(){
        // var _this = this;
        // _this.reportVisabled = false;
        // _this.$emit("opinionReport", this.reportIndex);
        // _this.reportedVisabled = true;
        // setTimeout(() => {
        //     _this.reportedVisabled = false;
        //     _this.$emit("opinionMenuHide", this.reportIndex);
        // }, 600);
        this.$emit("opinionReport");
    },
    reportClose: function(){
        // this.reportVisabled = false;
        // this.$emit("opinionMenuHide", this.reportIndex);
        this.$emit("reportClose");
    },
    reportedClose: function(){
        // this.reportedVisabled = false;
        this.$emit("reportedClose");
    },
    showReportModal:function(index){
        // this.reportIndex = index;
        // this.reportVisabled = true;
        this.$emit("showReportModal");
    },
    showReportedModal:function(index){
        // this.reportedVisabled = true;
        this.$emit("showReportModal");
    },
    showDeleteDialog:function(){
        // this.shouldshowDeleteDialog = true;
        this.$emit("showDeleteDialog");
    },
    deleteCancel:function(){
        // this.shouldshowDeleteDialog = false;
        // this.$emit("myCommentMenuHide");
        this.$emit("deleteCancel");
    },
    deleteMycomment:function(){
        this.$emit("deleteMycomment");
        // this.$emit("myCommentMenuHide");
        // this.shouldshowDeleteDialog = false;
    },
    editComment:function(){
        var _this = this;
        _this.$emit("myCommentMenuHide");
        _this.$emit("goToComment");
    }
  },
  created: function () {
  },
  mounted: function () {
  },
  activated: function () {
  }
};
</script>

