<template>
  <div class="articleDetail" >
    <QADetail v-if="isQADetail" />
    <ADDetail v-if="isADDetail"/> 
  </div>
</template>

<script>
import StateHelper from '@/animation/state-helper';
import QADetail from '@/components/QADetail.vue';
import ADDetail from '@/components/ADDetail.vue';

export default {
  name: 'articleDetail',
  data() {
    return {
      isQADetail:true,
      isADDetail:false,
      shouldShowBackButton: false,
      maskWidth:0,
      maskHeight:0,
      maskTop:0,
      maskLeft:0,
      isShow:false
    }
  },
  components: {
    QADetail,
    ADDetail
  },
  computed: {
  },
  mounted: function() {
  },
  activated: function() {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
    }
    this.shouldShowBackButton = this.$store.getters.shouldShowGoBack
  },
  methods: {
    gotoUserActive: function() {
      this.$router.push({ name: 'userActive', params: { userId: 123456} })
    },
    goBack: function() {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    close: function() {
      if (this.$store.getters.canGoback) {
        this.$router.go(this.$store.getters.gobackCount)
      } else {
        this.$router.push('/')
      }
    },
    showMask(event){
      var ele = event.currentTarget;
      this.maskWidth = ele.scrollWidth;
      this.maskHeight = ele.scrollHeight;
      var offsetLeft = ele.offsetLeft;
      var offsetTop = ele.offsetTop;
      var needContinue = true;
      while(needContinue){
          if(getComputedStyle(ele).position !="absolute" && ele.parentElement){
            ele = ele.parentElement;
              if(ele.className != "vue-pull-to-wrapper"){
                offsetLeft = ele.offsetLeft + offsetLeft;
                offsetTop = ele.offsetTop + offsetTop;
              }
              needContinue = true;
          }else{
              needContinue = false;
          }
      }
      this.maskTop = offsetTop;
      this.maskLeft = offsetLeft;
      this.isShow = true;
    },
    hideMask(){
      console.log("hideMask run");
      this.maskWidth = 0;
      this.maskHeight = 0;
      var offsetLeft = 0;
      var offsetTop = 0;
      this.isShow = false;
    }
  }
}
</script>

