<template>
  <transition name="modal-fade">
    <div class="modal-backdrop" @click="close">
      <div class="triangle"></div>
      <div class="modal" @click.stop role="dialog" aria-labelledby="modalTile" arial-describedby="modalDescription">
        <div class="modal-header" id="modalTitle">ジャンルで絞り込む</div>
        <div class="modal-body" id="modalDescription">
          <div :class="['modal-block', activeId == genre.genreId ? 'active' : '', (index == menuGenres.length - 1)? 'lastBlock' : '']" @click="chooseGenre(genre.genreId, genre.genreName)" v-for="(genre, index) in menuGenres" :key="index">
            <div class="blockCheck" v-if="activeId == genre.genreId">
              <img class="blockCheckImg" src="@/assets/medyImages/modal-check.png" srcset="@/assets/medyImages/modal-check@2x.png 2x, @/assets/medyImages/modal-check@3x.png 3x"/>
            </div>  
            <span class="blockName">{{genre.genreName}}</span>
          </div>
        </div>
        <div class="modal-bottom"></div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'genresModal',
  props: {
    menuGenres: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      activeId: 0
    }
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      this.activeId = 0;
    },
    close: function () {
      this.$emit('close');
    },
    chooseGenre: function (genreId, genreName) {
      if(this.activeId == genreId || typeof(this.$parent.isArticleLoading) == "undefined" || this.$parent.isArticleLoading){
        return;
      }
      this.activeId = genreId;
      this.$emit("changeLoadingFlag", true);
      this.$emit('changeGenre', genreId, genreName);
      this.$emit('close');
    }
  }
}
</script>

<style scoped>
.modal-fade-enter,
.modal-fade-leave-active {
  opacity: 0;
}
.modal-fade-enter-active,
.modal-fade-leave-active {
  transition: opacity 0.5s ease;
}
.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--black-mask);
  display: inline-block;
  z-index: 2;
}
.modal {
  background-color: var(--white-two);
  box-shadow: 2px 2px 20px 1px;
  display: inline-block;
  position: absolute;
  right: 0;
  top: 45px;
  width: 195px;
  height: 576px;
  overflow: hidden;
}
.modal-backdrop .triangle {
  border: 8px solid transparent;
  border-bottom: 12px solid var(--white-three);
  width: 0;
  height: 0;
  right: 17px;
  position: absolute;
  top: 25.5px;
  z-index: 1;
}
.modal-header{
  height: 18px;
  display: inline-block;
  width: calc(100% - 12px);
  padding: 9px 0 9px 12px;
  font-size: 12px;
  line-height: 18px;
  color: var(--black);
  background-color: var(--white-three);
  box-shadow: inset 0 -1px 0 0 var(--white-three);
}

.modal .modal-body{
  position: relative;
  height: 540px;
  overflow-y: scroll;
  width: 100%;
}
.modal-block {
  height: 18px;
  display: inline-block;
  width: calc(100% - 12px);
  padding: 9px 0 9px 12px;
  font-size: 12px;
  line-height: 18px;
  background: var(--white-two);
  box-shadow: inset 0 -1px 0 0 var(--white-three);
  position: relative;
}

.modal-block .blockCheck{
  position: relative;
  display: inline-block;
  width: 16px;
  height: 16px;
  overflow: hidden;
  margin-right: 2px;
}

.modal-block .blockCheck .blockCheckImg{
  width: 16px;
  position: absolute;
  border-right: 16px solid transparent;
  filter: drop-shadow(var(--white-two) 16px 0px);
  left: -16px;
}

.modal-block .blockName{
  font-size: 12px;
  line-height: 18px;
  display: inline-block;
  height: 18px;
  vertical-align: top;
}

.modal-block.lastBlock{
  box-shadow: none;
}

.modal-block.active {
  background-color: var(--tealish);
  color: var(--white-two);
}

.modal .modal-bottom {
  position: absolute;
  bottom: 0px;
  height: 36px;
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(0, 0, 0, 0.25));
  width: 100%;
  pointer-events: none;
}
</style>