<template>
  <div :class="['pickup-userArea', (isNoData || isLoadingFail) ? 'special' : '']">
    <div class="userTitle">
      <span>{{title}}</span>
    </div>
    <div class="userSort">
      <span :class="!activeWeekFlag ? 'active' : ''" @click="switchTab('day')">昨日</span>
      <div class="split"></div>
      <span :class="activeWeekFlag ? 'active' : ''" @click="switchTab('week')">週間</span>
    </div>
    <div :class="['userInfoArea' , (isNoData || isLoadingFail) ? 'special' : '']">
      <div :class="['userInfoScrollArea', (isNoData || isLoadingFail) ? 'special' : '']" ref="scrollXArea">
        <div class="userInfoAreaScroll" ref="userWidthCol">
          <pickUp-user :userInfo="item" :sortNo="index + 1" v-for="(item, index) in userInfos" :key="index"/>
          <div :class="['special', (isNoData)? 'left' : '']" v-if="(isNoData || isLoadingFail) && !isLoading">
            <div class="clickArea" @click="reLoadData">
              <div class="refresh" v-if="isLoadingFail">
                <img src="@/assets/medyImages/refresh-small.png" srcset="@/assets/medyImages/refresh-small@2x.png 2x, @/assets/medyImages/refresh-small@3x.png 3x">
              </div>
              <span>{{specialText}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PickUpUser from "@/components/cmptsCommon/PickUpUser.vue"

export default {
  name: 'pickUpUsers',
  props: {
    menuId: Number
  },
  data() {
    return {
      title: '',
      activeWeekFlag: false,
      isLoading: false,
      isNoData: false,
      isLoadingFail: false,
      userInfos: [],
      specialText: 'コンテンツがありません。',
      fakeUserInfos: [
        {
          userId: "100000163",
          userImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "ニックネーム10文字",
          jobName: "診察科診察科診察科診察科診察科診察科",
          thanksCount: "12",
        },
        {
          userId: "100000163",
          userImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "ニックネーム10文字",
          jobName: "診察科診察科診察科診察科診察科診察科",
          thanksCount: "12",
        },
        {
          userId: "100000163",
          userImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "ニックネーム10文字",
          jobName: "診察科診察科診察科診察科診察科診察科",
          thanksCount: "12",
        },
        {
          userId: "100000163",
          userImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "ニックネーム10文字",
          jobName: "診察科診察科診察科診察科診察科診察科",
          thanksCount: "12",
        },
        {
          userId: "100000163",
          userImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          userName: "ニックネーム10文字",
          jobName: "診察科診察科診察科診察科診察科診察科",
          thanksCount: "12",
        }
      ],
      userScrollLeft: 0
    };
  },
  components: {
    "pickUp-user": PickUpUser
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      this.$refs.scrollXArea.scrollLeft = this.userScrollLeft;
      this.userScrollLeft = 0;
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      this.activeWeekFlag = false;
      this.isNoData = false;
      this.isLoadingFail = false;
      this.specialText = 'コンテンツがありません。';
      // メニューIDについて、タイトルをセット
      if(this.menuId == this.Global.MENU_ID_CLINIC){
        this.title='臨床Q&A USERS';
      } else if(this.menuId == this.Global.MENU_ID_NEWS){
        this.title='医療ニュースコメント　USERS';
      }
      // ユーザーブロックをリセット
      this.userScrollLeft = 0;
      this.$refs.scrollXArea.scrollLeft = 0;
      // ユーザーデーターを取得
      this.getUserInfosByTab();
    },
    switchTab: function (flag) {
      if (this.isLoading) {
        return;
      }
      this.isLoading = true;
      // タブを切り替え
      if (flag == "day" && this.activeWeekFlag) {
        this.activeWeekFlag = false;
        this.getUserInfosByTab();
      } else if (flag == "week" && !this.activeWeekFlag) {
        this.activeWeekFlag = true;
        this.getUserInfosByTab();
      } else {
        this.isLoading = false;
      }
    },
    getUserInfosByTab: function (loaded = null) {
      var _this = this;
      if(loaded == null){
        _this.userInfos = [];
      }
      //TODO AJAX
      setTimeout(function () {
        
        // ユーザーブロックの幅を取得
        //TODO DELETE
        if(_this.activeWeekFlag){
          if(true){
            _this.isNoData = true;
            _this.isLoadingFail = false;
            _this.specialText = 'コンテンツがありません。';
          } else {
            _this.isNoData = false;
            _this.isLoadingFail = true;
            _this.specialText = '再読み込み';
          } 
          
        } else {
          _this.isNoData = false;
          _this.isLoadingFail = false;
          _this.userInfos = _this.fakeUserInfos;
        }
        var userCount = _this.userInfos.length;
        if(typeof(_this.$refs.userWidthCol) != "undefined" && _this.$refs.userWidthCol != null){
          _this.$refs.userWidthCol.style.width = 168 * userCount + 16 + 'px';
        }
        _this.isLoading = false;  
      }, 1000);
    },
    setScrollX: function () {
      this.userScrollLeft = this.$refs.scrollXArea.scrollLeft;
    },
    reLoadData: function(){
      if(!this.isLoadingFail){
        return;
      }
      this.isLoading = true;
      this.getUserInfosByTab();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pickup-userArea {
  width: 100%;
  height: 300px;
  background-color: var(--white);
  overflow: hidden;
}

.pickup-userArea.special {
  height: auto;
}

.pickup-userArea .userTitle {
  padding-top: 20px;
  padding-left: 12px;
  width: 100%;
  height: 36px;
  line-height: 36px;
}

.pickup-userArea .userTitle span {
  height: 27px;
  font-size: 18px;
  font-weight: 500;
  color: var(--black);
}

.pickup-userArea .userSort {
  height: 24px;
  margin-left: calc((100% - 81px));
  margin-top: 5px;
}

.pickup-userArea .userSort span {
  margin-top: 2px;
  width: 60px;
  height: 18px;
  font-size: 12px;
  font-weight: 300;
  color: var(--brown-grey);
}

.pickup-userArea .userSort .split {
  width: 1px;
  height: 8px;
  background-color: var(--black);
  margin-left: 10px;
  margin-right: 10px;
  display: inline-block;
}

.pickup-userArea .userSort .active {
  color: var(--black);
}

.userInfoArea {
  width: 100%;
  height: 210px;
  overflow-y: hidden;
}

.userInfoArea.special {
  height: auto;
}

.userInfoScrollArea {
  width: 100%;
  height: 220px;
  overflow: auto;
  overflow-y: hidden;
  min-height: 52px;
}

.userInfoArea.userInfoScrollArea.special{
  height: auto;
}

.userInfoArea .userInfoAreaScroll {
  min-width: 100%;
}

.userInfoArea .special{
  text-align: center;
  margin-top: 16px;
  margin-bottom: 16px;
  height: 20px;
}

.userInfoArea .special span{
  font-size: 14px;
  height: 20px;
  color: var(--black);
  line-height: 20px;
  vertical-align: top;
  display: inline-block;
}

.userInfoArea .special .refresh{
  width: 16px;
  height: 16px;
  margin-right: 8px;
  padding-top: 2px;
  display: inline-block;
}

.userInfoArea .special .clickArea{
  display: inline-block;
}

.userInfoArea .userInfoAreaScroll .special.left{
  text-align: left;
  padding-left: 12px;
}
</style>
