import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  modelPageCount: 0,
  isPageBack: false,
  isMenuShow: false,
  isForbiddenPullDown: false,
  interestData: [],
  overSeasData: [],

}

const mutations = {
  incrementModelPageCount (state) {
    state.modelPageCount++
  },
  decrementModelPageCount (state) {
    state.modelPageCount--
  },
  initModelPageCount (state) {
    state.modelPageCount = 1
  },
  clearModelPageCount (state) {
    state.modelPageCount = 0
  },
  saveIsPageBack (state, value) {
    state.isPageBack = value
  },
  showMenu (state) {
    state.isMenuShow = true;
  },
  hideMenu (state) {
    state.isMenuShow = false;
  },
  makeScrollForbiddenPullDown (state) {
    state.isForbiddenPullDown = true;
  },
  makeScrollCanPullDown (state) {
    state.isForbiddenPullDown = false;
  },
  setInterestArray (state, msg) {
    state.interestData = msg
  },
  setOverSeasArray (state, msg) {
    state.overSeasData = msg
  }
}

const getters = {
  canGoback: state => {
    return state.modelPageCount > 0;
  },
  gobackCount: state => {
    return 0 - state.modelPageCount;
  },
  shouldShowGoBack: state => {
    return state.modelPageCount > 1;
  },
  isShowMenu: state => {
    return state.isMenuShow;
  }
}

const actions = {
  showSideMenu({commit}){
    commit('showMenu')
  },
  hideSideMenu({commit}){
    commit('hideMenu')
  }
}

export default new Vuex.Store({
  state,
  mutations,
  getters,
  actions
})