          :class="{inputword:wordinput}"
          ref="keyWordInput"
          @keyup.enter="addKeyword()"
          @input="valueChange()" 
        />
        <!-- @change="checkeyword()" -->
         <!-- :placeholder="placeholder" -->
        <div class="placWord" @click="getFocus()">{{placeholder}}</div>
        <!-- 光标为×的显示 -->

        <!-- @keyup.enter='addKeyword()'定义enter键确定 -->
        <!-- onkeydown="this.onkeyup();" onkeyup="this.size=(this.value.length>4?this.value.length:4);" size="4" -->
        <!-- <div class="heartLeft">心臓</div> -->
        <div class="heartRight" @click="deleteKeyword()">
          <svg
            id="deleteKeyword"
            class="deleteIcon"
