<script>
import CommonModal from '@/components/cmptsModal/CommonModal.vue'
export default {
  name:'keyWordSet',
   components: {
    CommonModal,
  },
  data() {
    return {
      //   tempText:this.text,
      left: "キャンセル",
      center: "キーワードを設定",
      right: "完了",
      keywordAdd: "キーワード追加",
      informationSize: "※最大8個まで設定可能です",
      informationLong: "※キーワードは15文字まで入力できます",
      placeholder: "キーワードを入力してください",
      keyWordData: [],
      disabled: false,
      disabled1: false,
      dataInfo: "请输入有效值",
      wordinput: false,
      modalPattern: 'keyWord',
      isChecked: false,
      clearFlag: false,
       title: '',
    };
  },
  activated: function() {
      if (this.$refs.keyWordInput.value != '') {
        // this.clearFlag = true
       this.placeholder = ''
      } else {
        // this.clearFlag = false
        this.placeholder =  'キーワードを入力してください'
      }
  },
  methods: {
    leftButton: function() {
      this.$router.push({ path: "/medicalNewsSetting" });
    },
    rightButton: function() {
      this.$router.push({ path: "/medicalNewsSetting" });
    },
    goBack: function() {
      if (window.history.length > 1) {
        this.$router.go(-1);
      } else {
        this.$router.push("/");
      }
    },
    addKeyword() {
      if (this.$refs.keyWordInput.value != "") {
          console.log(this.$refs.keyWordInput.value);
          if (this.keyWordData.length < 8) {
            this.keyWordData.push(this.$refs.keyWordInput.value);
            this.$refs.keyWordInput.value=""
            this.disabled = false;
            this.disabled1 = false;
            //  console.log(this.keyWordData.length)
          } else {
            this.disabled = true;
            this.disabled1 = true;
          }
          if(this.keyWordData.length == 8){
            this.$refs.keyWordInput.value=""
            this.placeholder = ''
            this.disabled = true;
            this.disabled1 = true;
          }
          else{
            this.disabled1 =false;
            this.disabled = false;
          }
      } else {
        this.isChecked = true
      }

      if (this.$refs.keyWordInput.value != '') {
        // this.clearFlag = true
       this.placeholder = ''
      } else {
        // this.clearFlag = false
        this.placeholder =  'キーワードを入力してください'
      }
    },
    deleteKeyword() {
      this.$refs.keyWordInput.value = "";
    },
    FirImag(index) {
      this.keyWordData.splice(index, 1);
      if (this.keyWordData.length < 8) {
        this.disabled = false;
        this.disabled1 = false;
      }
    },
    closeModal() {
      this.isChecked = false
    },
    getFocus () {
      this.$refs.keyWordInput.focus()
    },
    valueChange(){
       if (this.$refs.keyWordInput.value != '') {
        // this.clearFlag = true
       this.placeholder = ''
      } else {
        // this.clearFlag = false
        this.placeholder =  'キーワードを入力してください'
      }
      var cnReg = /([\u4e00-\u9fa5]|[\u3000-\u303F]|[\uFF00-\uFF60])/g;
      var mat = this.$refs.keyWordInput.value.match(cnReg);
      if (mat) {
      var sChange = mat.length*2 + (this.$refs.keyWordInput.value.length - mat.length)
      } else {
      sChange = this.$refs.keyWordInput.value.length
      }
      if (sChange>30) {
      var arr = this.title.split('') //　输入字符串转为数组，依次推出最后一位元素
      for (var i = arr.length - 1; i >= 0; i--) {
      arr = arr.slice(0, i) // 每推出一个，将数组转为字符串，做一次占位数判断
      this.title = arr.join('')
      }
      // alert(this.$refs.keyWordInput.value)
      //  console.log(this.$refs.keyWordInput.value.length) 
    }
     console.log(sChange)
  }
}
}
</script>
