<template>
  <footer>
    <div class="footer">
      <div class="footerDiv" @click="switchTab('top')">
        <div class="footIconCol">
          <img :class="['icon', activeTab=='top' ? 'active' : '']" src="../assets/medyImages/home.png">
        </div>
        <span :class="['footerText', activeTab=='top' ? 'active' : '']">ホーム</span>
      </div>
      <div class="footerDiv" @click="switchTab('search')">
        <div class="footIconCol">
          <img :class="['icon', activeTab=='search' ? 'active' : '']" src="../assets/medyImages/search.png">
        </div>
        <span :class="['footerText', activeTab=='search' ? 'active' : '']">検索</span>
      </div>
      <div class="footerDiv" @click="switchTab('question')">
        <div class="footIconCol">
          <img :class="['icon', activeTab=='question' ? 'active' : '']" src="../assets/medyImages/mykarte.png">
        </div>
        <span :class="['footerText', activeTab=='question' ? 'active' : '']">マイカルテ</span>
      </div>
      <div class="footerDiv" @click="switchTab('timeline')">
        <div class="footIconCol">
          <img :class="['icon', activeTab=='timeline' ? 'active' : '']" src="../assets/medyImages/timeline.png">
        </div>
        <span :class="['footerText', activeTab=='timeline' ? 'active' : '']">タイムライン</span>
      </div>
      <div class="footerDiv" @click="switchTab('post')">
        <div class="footIconCol">
          <img :class="['icon', activeTab=='post' ? 'active' : '']" src="../assets/medyImages/cliplist.png">
        </div>
        <span :class="['footerText', activeTab=='post' ? 'active' : '']">クリップポスト</span>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  name: "medyFooter",
  data() {
    return {
      activeTab: 'top',
      tabId: 0,
    };
  },
  created: function () {
  },
  mounted: function () {
    this.init();
  },
  activated: function(){
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function(){
      this.activeTab = this.$router.currentRoute.name
    },
    switchTab: function(tab){
      if(tab == this.activeTab){
        return;
      }
      this.activeTab = tab;
      if(tab == 'top'){
        this.$router.push({ name: 'top'})
      } else if (tab == 'search') {
        this.$router.push({ name: 'search', params: { tabId: this.tabId }})
      } else if (tab == 'timeline') {
        this.$router.push({ name: 'timeline'})
      }
    }
  }
};
</script>

<style scope>
footer {
  position: fixed;
  left: 0px;
  bottom: 0px;
  width: 100%;
  height: 46px;
  background-color: var(--white-two);
  z-index: 5;
}

.footer {
  overflow: hidden;
  height: 46px;
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  display: flex;
  background-color: var(--white-two);
}

.footer .footerDiv {
  width: calc((100%) / 5);
  position: relative;
}
.footer .footerDiv .footIconCol{
  position: absolute;
  left: 50%;
  bottom: 17px;
  width: 24px;
  height: 24px;
  overflow: hidden;
  transform: translateX(-50%);
}

.footer .footerDiv .icon {
  position: relative;
  width: 24px;
  border-right: 24px solid transparent;
  filter: drop-shadow(var(--tealish) 24px 0px);
}

.footer .footerDiv .icon.active{
  left: -100%;
}

.footer .footerDiv .footerText{
  position: relative;
  top: 23px;
  height: 13px;
  font-size: 9px;
  color: var(--black);
}

.footer .footerDiv .footerText.active{
  color: var(--tealish);
}
</style>