<template>
  <div class="medical-opinions">
    <div class="opinionsHeaderCol">
      <div class="opinionsHeader" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'">
        <div class="back" @click="goBack()">
          <img src="@/assets/medyImages/icon-navbar-back.png" srcset="@/assets/medyImages/icon-navbar-back@2x.png 2x, @/assets/medyImages/icon-navbar-back@3x.png 3x"/>
        </div>

        <div class="opinionsTitle">
          <span class="opinionsText">Opinions</span>
        </div>

        <div class="search" @click="goSearch()">
          <img src="@/assets/medyImages/search-white.png" srcset="@/assets/medyImages/search-white@2x.png 2x, @/assets/medyImages/search-white@3x.png 3x"/>
        </div>
      </div>
    </div>
    <div class="opinionsPullCol">
      <pull-to ref="opinionsListPull" @scroll="scrollForLoad" :is-bottom-bounce="false" :top-load-method="refresh" :topConfig="pageHeaderConfig">
        <div class="opinionsTopArea">
          <div class="topImgCol" :style="'background-image: linear-gradient(to bottom, rgba(51, 51, 51, 0.5), rgba(51, 51, 51, 0.5)),url(' + backGroundImg + ');'"></div>
          <div class="opinionsTop">
            <div class="opinionsTitleArea">
              <div class="introduceAreaTitle">Opinions</div>
              <div class="introduceAreaDetail">医療関連ニュースを医療従事者のコメントと共に掲載しています。</div>
            </div>
          </div>
        </div>
        <div class="articleListCol" ref="articleListCol">
          <ArticleList ref="articleListOpinions" :menuId="Global.MENU_ID_NEWS_OPINIONS" :isTop="false" @setListScrollTop="setListScrollTop" @changeLoadingFlag="changeLoadingFlag"/>
        </div>
      </pull-to>
    </div>
    <GoTopMark @goTop="goTop" v-if="isTopMarkShow"/>
  </div>
</template>

<script>
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"
import PullTo from "@/components/vue-pull-to/vue-pull-to.vue"
import GoTopMark from "@/components/cmptsCommon/GoTopMark.vue"
import BackGroundImg from "@/assets/medyImages/medicalOpinions_background.png"

export default {
  name: 'medicalOpinionsList',
  components: {
    ArticleList,
    GoTopMark,
    PullTo
  },
  data: function () {
    return {
      pageHeaderConfig: {
        pullText: 'リフレッシュ',
        triggerText: 'リフレッシュ',
        loadingText: 'リフレッシュ中',
        doneText: '',
        failText: '',
        loadedStayTime: 400,
        stayDistance: 50,
        triggerDistance: 70
      },
      listScrollTop: 0,
      isArticleLoading: false,
      backGroundImg: BackGroundImg,
      isTopMarkShow: false,
      tabId: 1,
    }
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
      if (this.listScrollTop > 0) {
        this.$refs.opinionsListPull.setBackScrollPos(this.listScrollTop);
        this.listScrollTop = 0;
      }
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      // pulltoの場合、メインブロックの高さ
      this.$refs.articleListCol.style.height = window.innerHeight - 200 + 'px';
      this.isArticleLoading = false;
      this.isTopMarkShow = false;
      this.$refs.opinionsListPull.scrollEl.addEventListener('scroll',this.isTopMarkCanShow);
    },
    goBack: function () {
      if (window.history.length > 1) {
        this.$router.go(-1)
      } else {
        this.$router.push('/')
      }
    },
    refresh: function (loaded) {
      //pull down refresh
      if (this.isArticleLoading) {
        loaded("fail");
        return;
      }
      this.$refs.articleListOpinions.getArticleListByGenre(0, loaded);
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.listScrollTop = this.$refs.opinionsListPull.getScrollPos();
    },
    goSearch: function () {
      // search pageに遷移する
      this.setListScrollTop();
      this.$router.push({ name: 'search', params: { tabId: 6 } });
    },
    scrollForLoad: function () {
      // pull up load article list
      if (this.isArticleLoading) {
        return;
      }
      this.isArticleLoading = true;
      let listHeight = this.$refs.articleListOpinions.$refs.listBlock.clientHeight;
      let outerHeight = this.$refs.articleListCol.clientHeight;
      let loadingPos = listHeight - outerHeight;
      if (listHeight - outerHeight > 0 && this.$refs.opinionsListPull.getScrollPos() >= loadingPos - 48) {
        this.$refs.articleListOpinions.getArticleList();
      } else {
        this.isArticleLoading = false;
      }
    },
    changeLoadingFlag: function (value) {
      this.isArticleLoading = value;
    },
    // goTopMark function
    isTopMarkCanShow: function(){
      this.isTopMarkShow = this.$refs.opinionsListPull.getScrollPos() >= 250;
    },
    goTop: function(){
      this.$refs.opinionsListPull.setBackScrollPos(0);
    }
  }
}
</script>

<style scope>
.medical-opinions .opinionsHeaderCol{
  height: 42px;
  position: fixed;
  top: 0px;
  width: 100%;
  display: inline-block;
  overflow: hidden;
  vertical-align: top;
}

.medical-opinions .opinionsHeader {
  height: 200px;
  width: 100%;
  display: inline-block;
  overflow: hidden;
  text-align: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.medical-opinions .opinionsHeader .back {
  width: 24px;
  height: 24px;
  display: inline-block;
  position: absolute;
  top: 8px;
  left: 8px;
  line-height: 26px;
  overflow: hidden;
}

.medical-opinions .opinionsHeader .back img {
  width: 7px;
}

.medical-opinions .opinionsHeader .opinionsTitle {
  display: inline-block;
  position: relative;
  top: 8px;
  height: 24px;
}

.medical-opinions .opinionsHeader .opinionsTitle .opinionsText {
  font-size: 16px;
  line-height: 21px;
  height: 24px;
  color: var(--white-two);
}

.medical-opinions .opinionsHeader .search {
  display: inline-block;
  position: absolute;
  right: 12px;
  top: 9px;
  width: 24px;
  height: 24px;
  overflow: hidden;
}

.medical-opinions .opinionsHeader .search img {
  width: 24px;
  position: relative;
  margin-top: -2px;
}

.medical-opinions .opinionsTopArea {
  position: relative;
  height: 103px;
  width: 100%;
  padding-top: 55px;
  overflow: hidden;
}

.medical-opinions .opinionsTopArea .topImgCol {
  position: absolute;
  height: 200px;
  width: 100%;
  top: -42px;
  background-repeat: no-repeat;
  background-size: cover;
}

.medical-opinions .opinionsTop {
  width: 100%;
  text-align: center;
}

.medical-opinions .opinionsTop .opinionsTitleArea {
  width: 100%;
  position: relative;
}

.medical-opinions .opinionsTop .opinionsTitleArea .introduceAreaTitle {
  width: 100%;
  height: 29px;
  font-size: 20px;
  font-weight: 500;
  line-height: 29px;
  color: var(--white-two);
  text-align: left;
  margin: 0 0 0 15px;
}

.medical-opinions .opinionsTop .opinionsTitleArea .introduceAreaDetail {
  width: calc(100% - 31px);
  height: 36px;
  font-size: 12px;
  color: var(--white-two);
  text-align: left;
  margin: 2px auto 0 16px;
}

.medical-opinions .opinionsPullCol {
  margin-top: 42px;
  overflow: hidden;
}

.medical-opinions .articleListCol {
  position: relative;
}
</style>
