<template>
  <div :class="['articleListArea', isTop? 'top' : 'list' , isListCanExist ? 'heightCol' : '']" ref="listBlock">
    <div class="articleList-tabTitle" v-if="isTop">
      <span>{{title}}</span>
      <img class="arrow" src="@/assets/medyImages/contents-more.png" srcset="@/assets/medyImages/contents-more@2x.png 2x, @/assets/medyImages/contents-more@3x.png 3x" @click="gotoArticleList">
    </div>
    <div v-if="isListCanExist">
      <div class="noArticleText" v-if="isNoData">コンテンツがありません。</div>
      <common-article :article="item" v-for="(item, index) in articles" :key="index" @setListScrollTop="setListScrollTop"/>
      <div class="showMore" v-if="isTop && isTopInitLoadingFinished && !isNoData">
        <div @click="gotoArticleList">
          <span>もっと見る</span>
          <img class="arrow" src="@/assets/medyImages/contents-more-small.png" srcset="@/assets/medyImages/contents-more-small@2x.png 2x, @/assets/medyImages/contents-more-small@3x.png 3x">
        </div>
      </div>
      <div :class="['refreshDiv', !isListInitLoadingFinished ? 'initLoad' : '']" v-if="isListCanExist && (!isTop || (isTop && isLoadingFail))&& !isNoData">
        <div class="clickForRefresh" @click="reLoadArticleList()">
          <div class="refresh" v-if="isLoadingFail">
            <img src="@/assets/medyImages/refresh-small.png" srcset="@/assets/medyImages/refresh-small@2x.png 2x, @/assets/medyImages/refresh-small@3x.png 3x">
          </div>
          <div class="retry">{{loadingText}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ArticleCommon from "@/components/cmptsCommon/ArticleCommon.vue"

export default {
  name: 'articleList',
  props: {
    menuId: Number,
    isTop: {
      type: Boolean,
      default: true
    },
    isListCanExist: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      title: '',
      isLoading: false,
      isLoadingFail: false,
      isTopInitLoadingFinished: false,
      isListInitLoadingFinished: false,
      isNoData: false,
      loadingText: 'loading...',
      scrollPos: 0,
      articles: [],
      genreId: 0,
      fakeArticles: [
        {
          articleId: "310001",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "0",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleStartDate: "5分前",
          articleType: 1
        },
        {
          articleId: "310001",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleStartDate: "20時間前",
          articleType: 1
        },
        {
          articleId: "310001",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "123",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleStartDate: "23時間前",
          articleType: 2
        },
        {
          articleId: "310001",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "0",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleStartDate: "2019年1月20日",
          articleType: 1
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleStartDate: "2018年12月30日",
          articleType: 2
        },
        {
          articleId: "310001",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          commentDisplayFlag: "0",
          articleTypeName: "特集",
          articleStartDate: "2018年12月28日",
          articleType: 1
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleStartDate: "2018年12月28日",
          articleType: 2
        },
        {
          articleId: "310001",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "0",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleStartDate: "2018年12月28日",
          articleType: 1
        },
        {
          articleId: "310002",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          commentDisplayFlag: "0",
          articleTypeName: "特集",
          articleStartDate: "2018年12月28日",
          articleType: 2
        },
        {
          articleId: "310001",
          articleImg: "https://s3-ap-northeast-1.amazonaws.com/image-picker-static/images/20160617015843/Fotolia_67955160_Subscription_Monthly_M.jpg",
          articleTitle: "タイトルタイトルタイトルタイトルタイトルタイトルタイトル最大２ラインまで",
          commentCount: "12",
          commentDisplayFlag: "1",
          articleTypeName: "特集",
          articleStartDate: "2018年12月28日",
          articleType: 1
        },
      ],
      fakeLoadingOk: true
    };
  },
  components: {
    "common-article": ArticleCommon
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
      this.init();
    }
  },
  methods: {
    init: function () {
      this.loadingText = 'loading...';
      if(this.isTop){
        if(this.menuId == this.Global.MENU_ID_CLINIC){
          this.title='臨床Q&A';
        } else if(this.menuId == this.Global.MENU_ID_NEWS_OPINIONS){
          this.title='Opinions';
        } else if(this.menuId == this.Global.MENU_ID_NEWS_MYMEDICALNEWS){
          this.title='Myメディカルニュース';
        }
      } 
      this.articles = [];
      this.isLoading = false;
      this.isLoadingFail = false;
      this.isTopInitLoadingFinished = false;
      this.isListInitLoadingFinished = false;
      this.isNoData = false;
      this.genreId = 0;
      //TODO DELETE
      this.fakeLoadingOk = true;
      //すべて記事リストを取得
      this.getArticleList();
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.$emit('setListScrollTop');
    },
    gotoArticleList: function () {
      // 臨床qaの一覧を遷移する
      this.setListScrollTop();
      if(this.menuId == this.Global.MENU_ID_CLINIC){
        this.$router.push({ name: 'clinicalList'});
      } else if(this.menuId == this.Global.MENU_ID_NEWS_OPINIONS) {
        this.$router.push({ name: 'medicalOpinions'});
      } else if(this.menuId == this.Global.MENU_ID_NEWS_MYMEDICALNEWS){
        this.$router.push({ name: 'medicalNews'});
      }
    },
    getArticleListByGenre: function(genreId, loaded = null){
      this.isLoadingFail = false;
      this.isListInitLoadingFinished = false;
      this.loadingText = 'loading...';
      this.isNoData = false;
      this.genreId = genreId;
      
      //TODO DELETE
      if(genreId == 4){
        this.fakeLoadingOk = false;
      } else {
        this.fakeLoadingOk = true;
      }

      if(loaded == null){
        this.articles = [];
      }
      
      this.getArticleList(loaded); 
    },
    getArticleList: function (loaded=null) {
      var _this = this;
      if(!_this.isListCanExist){
        return;
      }
      if (_this.isLoadingFail) {
        _this.$emit("changeLoadingFlag", false);
        return;
      }
      //TODO AJAX
      setTimeout(() => {
        if(_this.isTop){
          //TOP の 場合
          if(true){
            //data.result = '0000'
            // successのばあい
            _this.isLoadingFail = false;
            if(_this.articles.length == 0 && false){
              //data.articles.length == 0
              _this.isNoData = true;
            } else {
              _this.articles = _this.fakeArticles;
            }
            if(loaded !== null){
              loaded('done');
            }
            _this.isTopInitLoadingFinished = true;
          } else {
            // fail の場合
            _this.isLoadingFail = true;
            _this.loadingText = '再読み込み';
            if(loaded != null){
              loaded("fail");
            }
          }
        } else {
          if(_this.fakeLoadingOk){
            _this.isLoadingFail = false;
            _this.loadingText = 'loading...';
            if(_this.articles == 0 && (_this.genreId !== 0 && _this.genreId !== 1 && _this.genreId !== 2)){
              //data.articles.length == 0
              _this.isNoData = true;
            } else {
              if(loaded != null){
                _this.articles = _this.fakeArticles;
              } else {
                 _this.articles = _this.articles.concat(_this.fakeArticles);
              //TODO AJAX CONTROL
                if(_this.isListInitLoadingFinished){
                  _this.articles = _this.articles.concat(_this.fakeArticles);
                  _this.articles = _this.articles.concat(_this.fakeArticles);
                }
              }
             
              //TODO DELETE
              if(_this.genreId == 2){
                _this.fakeLoadingOk = false;
              }
            }
            if(loaded !== null){
              loaded('done');
            }
            _this.isListInitLoadingFinished = true;
          } else {
            // fail の場合
            _this.isLoadingFail = true;
            _this.loadingText = '再読み込み';
            if(loaded != null){
              loaded("fail");
            }
          }
        }
        _this.$emit("changeLoadingFlag",false);
      }, 2000);
    },
    reLoadArticleList: function () {
      // load fail の場合、リロード
      if(this.isLoadingFail && this.isTop){
        this.isLoadingFail = false;
        this.getArticleList();
      } else if(this.isLoadingFail && typeof(this.$parent.$parent.isArticleLoading) != "undefined" && !this.$parent.$parent.isArticleLoading){
        this.$emit("changeLoadingFlag", true);
        this.isLoadingFail = false;
        this.loadingText = 'loading...';
        this.getArticleList();
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.articleListArea.list {
  background-color: var(--white-two);
  position: relative;
}

.articleListArea.top {
  background-color: var(--white-two); 
}

.articleListArea.top.heightCol {
  min-height: 112px;
}

.articleListArea.top .articleList-tabTitle {
  padding-top: 20px;
  padding-left: 12px;
  height: 36px;
  position: relative;
  font-size: 18px;
  font-weight: 500;
  color: var(--black);
}

.articleListArea.top .articleList-tabTitle span{
  display: inline-block;
  position: relative;
  margin: 4px auto 5px auto;
  height: 27px;
  line-height: 27px;
  vertical-align: top;
}

.articleListArea.top .articleList-tabTitle .arrow {
  position: relative;
  width: 24px;
  height: 24px;
  margin-left: 8px;
  object-fit: contain;
  margin-top: 6px;
  display: inline-block;
}

.articleListArea.top .showMore {
  padding-top: 16px;
  height: 56px;
  font-size: 13px;
  font-weight: 300;
  position: relative;
  color: var(--brownish-grey);
  display: inline-block;
  margin-left: calc((100% - 90px) / 2);
}

.articleListArea.top .showMore .arrow {
  margin-top: 2px;
  margin-left: 8px;
  width: 16px;
  height: 16px;
  object-fit: contain;
  vertical-align: top;
}

.articleListArea .refreshDiv {
  width: 100%;
  height: 36px;
  margin: 0px auto 0px auto;
  text-align: center;
  position: relative;
  background: var(--white-two);
  padding-top: 12px;
}

.articleListArea.list .refreshDiv.initLoad{
  background-color: var(--ice-blue);
}

.articleListArea .refreshDiv .clickForRefresh{
  display: inline-block;
}

.articleListArea .refreshDiv .refresh {
  width: 16px;
  height: 16px;
  display: inline-block;
  margin-right: 8px;
  padding-top: 1px;
}

.articleListArea .refreshDiv .retry {
  width: 65px;
  height: 18px;
  font-size: 13px;
  font-weight: 300;
  color: var(--brownish-grey);
  display: inline-block;
  line-height: 18px;
  position: relative;
  vertical-align: top;
}

.articleListArea .noArticleText{
  text-align: left;
  background: var(--ice-blue);
  height: 36px;
  color: var(--black);
  font-size: 14px;
  font-weight: 300;
  padding-top: 10px;
  padding-left: 12px;
}

.articleListArea.top .noArticleText{
  height: 52px;
  line-height: 52px;
  background: var(--white-two);
  padding-top: 0px;
}
</style>
