<template>
  <div class="clincalTop" v-if="isCheckIn">
    <new-articles :menuId="menuId" @setListScrollTop="setListScrollTop" ref="newArticles"/>
    <div class="clincal-qaLabel">
      <div class="qaImgArea" @click="gotoArticleList">
        <img class="qaImg" src="@/assets/medyImages/button-movebanner.png" srcset="@/assets/medyImages/button-movebanner@2x.png 2x, @/assets/medyImages/button-movebanner@3x.png 3x"/>
      </div>
    </div>
    <pickUp-articles ref="pickUpArticles" :menuId="menuId" @setListScrollTop="setListScrollTop"/>
    <pickUp-users ref="pickUpUsers" :menuId="menuId" @setListScrollTop="setListScrollTop"/>
    <article-list ref="articleList" :menuId="menuId" @setListScrollTop="setListScrollTop"/>
    <div class="clincal-bottom"></div>
  </div>
  <div class="welcome" v-else>
    <div class="hello">
      <p class="p1">臨床Q&amp;A</p>
      <p class="p2">医者ユーザー4万人と話し合おう</p>
      <p class="p3">臨床の場でふと感じた疑問や不安。</p>
      <p class="p4">他の医者の考えや対処を知ることが、次への一歩につながる。</p>
    </div>
    <div class="checkIn">
      <p class="p5">こちらのメニューは医師でご登録頂いており本人認証が完了している方のみご利用可能です。</p>
      <div class="link" align="center">
        <a class="stackoverflow" href="javascript:void(0);" @click="changeOptinFlag">
          <button class="check">本人認証はこちらから</button>
        </a>
        <p class="p6">(medパス登録画面に遷移します)</p>
      </div>
    </div>
  </div>
</template>

<script>
import NewsArticles from "@/components/cmptsClinical/NewsArticles.vue"
import PickUpArticles from "@/components/cmptsClinical/PickUpArticles.vue"
import PickUpUsers from "@/components/cmptsClinical/PickUpUsers.vue"
import ArticleList from "@/components/cmptsClinical/ArticleList.vue"

export default {
  name: 'clinicalTop',
  props: {
    //TODO DELETE
    isSecond: {
      type: Boolean,
      default: false
    }
  },
  data: function () {
    return {
      isCheckIn: true,
      menuId: this.Global.MENU_ID_CLINIC
    }
  },
  components: {
    'new-articles': NewsArticles,
    'pickUp-articles': PickUpArticles,
    'pickUp-users': PickUpUsers,
    'article-list': ArticleList,
  },
  mounted: function () {
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      // gobackの場合、keep-alliveでcacheを利用する、画面をrefreshしない
    } else {
      // 普通遷移の場合、画面初期化処理を行う
    }
  },
  methods: {
    init: function () {
      // TODO AJAX GET CHECKIN
      this.isCheckIn = this.isSecond;
    },
    setListScrollTop: function () {
      // pulltoの場合、scrolltopを銘記
      this.$emit('setListScrollTop');
      this.$refs.newArticles.setScrollX();
      this.$refs.pickUpUsers.setScrollX();
    },
    gotoArticleList: function () {
      // 記事リストに遷移する
      this.setListScrollTop();
      this.$router.push({ name: 'clinicalList'});
    },
    refresh: function (loaded) {
      this.init();
      if(this.isCheckIn){
        this.$refs.newArticles.getNewsArticles();
        this.$refs.pickUpArticles.getArticleByTab(loaded);
        this.$refs.pickUpUsers.getUserInfosByTab(loaded);
        this.$refs.articleList.getArticleList(loaded); 
      }
    },
    changeOptinFlag: function(){
      //TODO DELETE
      // TODO AJAX
      this.$emit("changeOptinFlag");
      this.isCheckIn = true;
    }
  }
}
</script>

