<script>
import CommonModal from '@/components/cmptsModal/CommonModal.vue'

export default {
  name: 'sideMenu',
  components:{
    CommonModal
  },
  props: {
    isOptinPage: false
  },
  data() {
    return {
      userInfo: Object,
      year: '2019',
      fakeUserInfo: {
        userImg: '',
        proDoctorFlag: '1',
        userName: '9文字制限なのかな',
        jobName: '美容・アンチエイジング',
        userAge: '40代',
        officeName: '泉ガーデンクリニック',
        surplusCount: '10',
        thanksCount: '112',
        followCount: '21',
        attentionCount: '8',
        point: '6112',
        ticket: '99'
      },
      isLogOutVisible: false,
      isNewAttent: false,
      modalPattern: 'logOut'
    }
  },
  mounted: function(){
    this.init();
  },
  activated: function () {
    if (this.$store.state.isPageBack) {
      this.init();
    } else {
      this.init();
    }
  },
  methods: {
    init: function(){
      var _this = this;
      this.$refs.linkArea.style.height = window.innerHeight - 365 + 'px';
      //TODO AJAX GET USERINFO
      setTimeout(function(){
        if(!_this.isOptinPage){
          _this.userInfo = _this.fakeUserInfo;
          _this.isNewAttent = true;
        }
        _this.year = '2019';
        
      }, 1000);
    },
    hideSide: function() {
      this.$store.dispatch('hideSideMenu');
    },
    toggleMenu: function(href) {
      this.hideSide();
      this.$router.push(href);
    },
    toggleLogOutModal: function(value){
      this.isLogOutVisible = value;
    },
    logOut: function(){
      
    },
    toInquiry:function(){
      this.$router.push('/inquiry') 
    },
    toTicket:function(){
      this.$router.push('/medyTicket') 
    },
    toMailSet:function(){
      this.$router.push('/mailset')
    }
  },
  computed: {
    isShowMenu: function() {
      return this.$store.getters.isShowMenu;
    }
  },
  watch: {
    isOptinPage: function(){
      var _this = this;
      if(!_this.isOptinPage){
          setTimeout(function(){
            _this.userInfo = _this.fakeUserInfo;
            _this.isNewAttent = true;
        }, 1000);
      }
    }
  }
}
</script>

